// Colors
$color-plum: #19010C;
$color-purple: #5F032E;
$color-scroll: #D4B99B;
$color-stone: #FFE9DE;
$color-blue: #040C28;

$color-black: #000;
$color-darker-grey: #666666;
$color-dark-grey: #c5c5c5;
$color-light-grey: #f5f5f5;
$color-white: #fff;

// Darkmode
$dm-text: #ececec;
$dm-boxes-hover: #313131;
$dm-boxes: #1b1b1b;
$dm-background: #111;
$dm-href: #ececec;

// Typography
$font-headings: 'Albertus Nova', sans-serif;
$font-copy: 'Basis Grotesque', sans-serif;
$heading-1: 48px;
$heading-2: 24px;

// Sizing
$header-height: 76px;

// Theming
$body-bg: $color-scroll;

$header-bg: $color-plum;
$header-color: $color-scroll;
$header-color-hover: $color-stone;

$footer-bg: $header-bg;
$footer-color: $header-color;
$footer-color-hover: $header-color-hover;

$link-color: $color-purple;
$link-color-hover: $color-plum;

$accent-color: $color-purple;

// Buttons
$button-bg: $color-scroll;
$button-color: $color-purple;
$button-border: $button-bg;
$button-bg-hover: $color-stone;
$button-border-hover: $button-bg-hover;
$button-color-hover: $button-color;

$button-outline-bg: transparent;
$button-outline-color: $color-scroll;
$button-outline-border: $button-outline-color;
$button-outline-bg-hover: $color-stone;
$button-outline-border-hover: $button-outline-bg-hover;
$button-outline-color-hover: $color-purple;

$button-dark-bg: $color-purple;
$button-dark-color: $color-stone;
$button-dark-border: $button-dark-bg;
$button-dark-bg-hover: $color-plum;
$button-dark-border-hover: $color-plum;
$button-dark-color-hover: $button-dark-color;

$button-dark-outline-bg: transparent;
$button-dark-outline-color: $color-purple;
$button-dark-outline-border: $color-purple;
$button-dark-outline-bg-hover: $color-plum;
$button-dark-outline-color-hover: $color-stone;
$button-dark-outline-border-hover: $color-plum;

