.featured-games {
    position: relative;
    padding-bottom: 70px;
    overflow: hidden;

    @media screen and (min-width: 768px) {
        padding-bottom: 100px;
    }

    .container {
        position: relative;
        z-index: 1;

        .col-sm-6 {
            @media screen and (max-width: 767px) {
                margin-bottom: 24px;
            }
        }
    }

    &__gradient {
        position: absolute;
        top: 0;
        left: 0;
        widtH: 100%;
        height: 100%;
        pointer-events: none;
    }

    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 70px;
        padding-bottom: 60px;

        @media screen and (min-width: 768px) {
            padding-top: 80px;
            padding-bottom: 70px;
        }

        .button {
            transform: translateY(-4px);
            display: none;
            @media screen and (min-width: 768px) {
                display: block;
            }
        }
    }

    &__footer {
        padding-top: 70px;

        @media screen and (min-width: 768px) {
            display: none;
            padding-top: 80px;
        }
    }

    &__item {
        display: block;
        position: relative;
        border-radius: 16px;
        overflow: hidden;

        @media screen and (min-width: 768px) {
            border-radius: 24px;
        }

        &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: $color-black;
            opacity: 0;
            pointer-events: none;
            transition: opacity 0.2s ease;
        }

        &:hover {
            &:after {
                opacity: 0.3;
            }

            .featured-games__image img {
                transition-duration: 4s;
                transform: scale3d(1.1, 1.1, 1.1);
            }
        }
    }

    &__image {
        padding-bottom: 90%;
        position: relative;
        overflow: hidden;

        img {
            position: absolute;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top;
            transform-origin: top;
            transition: transform 0.2s linear;
        }
    }

    &__content {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
        padding: 24px;

        h3 {
            margin: 0;
            color: $color-white;
            text-transform: none;
            font-size: clamp(24px, -1.25rem + 5vw, 40px);
        }
    }

    &__tags {
        color: $color-white;
        font-weight: 600;
        margin-bottom: 5px;

        @media screen and (min-width: 768px) {
            font-size: 20px;
        }
    }

}
