.about-us-hero {

}

.about-us-recruiters {
  position: relative;

  .section-grey-bg {
    top: 340px;
    bottom: 0;
    height: auto;

    @media screen and (max-width: 768px) {
      display: block;
    }
  }
}

.about-us-recruiters-columns {
  img {
    width: 100%;
    height: auto;
    margin-bottom: 24px;

    @media screen and (max-width: 768px) {
      margin-bottom: 10px;
    }
  }

  h3 {
    margin-bottom: 8px;
  }

  p {
    margin-bottom: 64px;

    @media screen and (max-width: 768px) {
      margin-bottom: 40px;
    }
  }

  a {
    @media screen and (max-width: 768px) {
      margin-bottom: 64px;
    }
  }

  .button {
    @media screen and (min-width: 768px) {
      position: absolute;
      bottom: 0;
    }
  }
}

.about-us-recruiters-heading {
  margin-bottom: 64px;
  align-items: flex-end;

  h2 {
    margin-bottom: 8px;
  }

  @media screen and (max-width: 768px) {
    margin-bottom: 48px;
  }
}

.about-us-people {
  &.section-padding-bottom {
    padding-bottom: 80px;

    @media screen and (max-width: 767px) {
      padding-bottom: 16px;
    }
  }

  img {
    width: 100%;
    height: auto;
    margin-bottom: 16px;
  }

  .cta {
    margin-bottom: 8px;
  }
}

.about-us-people-title {
  margin-bottom: 16px;

  color: #545454;
}

.about-us-people-places {
  margin-bottom: 80px;

  color: #545454;

  font-size: 16px;
  line-height: 24px;

  @media screen and (max-width: 768px) {
    margin-bottom: 48px;
  }
}

.about-us-map-container {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 640px;

  @media screen and (max-width: 768px) {
    height: auto;
    display: block;
  }
}

.about-us-map {
  width: calc(66.67% - 48px);

  @media screen and (max-width: 768px) {
    width: 100%;
  }

  img {
    object-fit: cover;

    @media screen and (max-width: 768px) {
      width: 100%;
      height: auto;
    }
  }
}

.about-us-list {
  width: calc(33.33% + 48px);

  background: $color-light-grey;

  @media screen and (max-width: 768px) {
    width: 100%;
  }
}

.about-us-content-wrapper {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  overflow-y: auto;

  @media screen and (max-width: 768px) {
    position: static;
    overflow: visible;
  }
}

.about-us-content {
  padding: 48px 0;

  @media screen and (max-width: 768px) {
    padding: 24px 15px;
    margin-left: -15px;
    margin-right: -15px;

    background: $color-light-grey;
  }

  select {
    width: 100%;
    height: 56px;
    padding: 16px 48px 16px 16px;
    margin-bottom: 24px;

    border: 0;
    border-radius: 4px;
    background: $color-white;
    color: $color-black;

    -moz-appearance: none;
    -webkit-appearance: none;

    @media screen and (max-width: 768px) {
      margin-bottom: 0;
    }
  }

  .dropdown-wrapper {
    @media screen and (max-width: 768px) {
      margin-bottom: 16px;
    }
  }

  .mobile-margin-top {
    @media screen and (max-width: 768px) {
      margin-top: 32px;
    }
  }
}

.about-us-place {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 16px;

  &:not(:last-child) {
    border-bottom: 1px solid #d0d0d0;

    @media screen and (max-width: 768px) {
      border-bottom: 1px solid $color-black;
    }
  }

  &:not(:first-child) {
    padding-top: 16px;
  }
}

.about-us-place-image-wrapper {
  display: flex;
  align-items: center;

  img {
    width: 88px;
    height: 88px;
    border-radius: 50%;
    margin-right: 16px;
  }
}

.about-us-place-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;

  .cta {
    margin-bottom: 32px;
    margin-top: 8px;
  }
}

p.about-us-place-address {
  margin: 0;

  font-size: 12px;
  line-height: 16px;
}

.about-us-place-meta {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;

  text-align: right;

  font-size: 12px;
  line-height: 16px;

  a {
    width: 48px;
    height: 48px;
    margin-bottom: 16px;

    img {
      width: auto;
      height: 16px;
    }
  }
}

.about-us-map-mobile-load-more {
  margin-top: 48px;
  margin-bottom: 48px;
}
