.social-channels {
    padding: 70px 0;
    background: $color-scroll;

    @media screen and (min-width: 768px) {
        padding-top: 80px;
        padding-bottom: 80px;
    }

    h2 {
        color: $color-purple;
        margin-bottom: 20px;
    }

    p {
        color: $color-purple;
    }

    &__links {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: 30px;

        @media screen and (min-width: 768px) {
            margin-top: 40px;
        }

        a {
            display: inline-flex;
            justify-content: flex-start;
            align-items: center;
            width: 100%;
            margin-bottom: 10px;
            font-size: 20px;
            color: $color-scroll;
            text-decoration: none;
            background: $color-purple;
            border-radius: 16px;
            padding: 12px 12px 12px 30px;

            img {
                margin-right: 20px;
            }

            &:hover {
                background: $color-plum;
            }

            @media screen and (min-width: 768px) {
                justify-content: center;
                width: calc(50% - 5px);
                padding: 14px;

                &:nth-child(3),
                &:nth-child(4),
                &:nth-child(5),
                &:nth-child(8),
                &:nth-child(9),
                &:nth-child(10),
                &:nth-child(13),
                &:nth-child(14),
                &:nth-child(15) {
                    width: calc(33% - 5px);
                }
            }
        }
    }
}
