.module-twitterbar {
  display: flex;
  margin: 20px 0 60px;
  .twitter-side-logo {
    display: inline-block;
    width: 100px;
    height: 80px;
    background: url(images/twitter-bubble.svg) no-repeat center;
    background-color: #f5f5f5;
  }
  .tns-outer {
    width: calc(100% - 100px);
    display: flex;
    flex-wrap: wrap;
    button[data-action="stop"],
    button[data-action="start"],
    .tns-liveregion {
      display: none;
      position: initial;
    }

    .tns-controls {
      height: 80px;
      width: 160px;
      background: #e4e4e4;
      display: flex;
      order: 3;

      button {
        position: relative;
        display: block;
        width: 80px;
        height: 80px;
        transform: initial;
        padding: 0;
        margin: 0;
        background-size: 24px 16px !important;

        &[data-controls="prev"] {
          background: url(images/chevron-down-black.svg) no-repeat;
          background-position: 40px 32px;
        }
        &[data-controls="next"] {
          background: url(images/chevron-up-black.svg) no-repeat;
          background-position: 16px 32px;
        }
      }
    }

    .tns-inner {
      width: calc(100% - 160px);
      background: $color-light-grey;

      .twitter-posts {
        width: calc(100% - 100px);
        position: relative;
        height: 80px;

        &::before {
          position: absolute;
          top: 0;
          left: 0;
          width: 100px;
          height: 80px;
          background-repeat: no-repeat;
          background-position: right center;
          color: $color-white;
          z-index: 2;
          font-size: 35px;
          line-height: 80px;
          text-align: center;
          background-size: cover;
        }
        .tns-horizontal.tns-subpixel > .tns-item {
          vertical-align: middle;
        }
      }

      .twitter-post {
        display: flex !important;
        flex-direction: column;
        height: 80px !important;
        font-size: 24px;
        color: $color-black;
        border: 0 !important;
        overflow: hidden;
        text-decoration: none;
        justify-content: center;

        .twitter-text span {
          color: $color-black !important;
        }
      }

      .twitter-image {
        display: none;

        img {
          display: none;
        }
      }

      .twitter-text {
        flex-grow: 0;
        display: flex;
        align-items: flex-end;
        flex-direction: column;
        margin: 10px 0;
        padding: 0 37px;
        font-size: 22px;
        font-weight: bold;
        letter-spacing: 0;
        line-height: 30px;

        span {
          display: block;
          color: $color-black !important;
        }

        .twitter-image + & {
          margin-top: 10px;
          height: 114px;
        }
      }
    }
  }

  @media screen and (max-width: 1024px) {
    flex-flow: column;
    margin: 20px 0 60px;
    .twitter-side-logo {
      order: 1;
      width: 50%;
      height: 56px;
      background: url(images/twitter-icon.svg) no-repeat 16px;
      background-color: #e4e4e4;
    }
    .tns-outer {
      flex-flow: column-reverse;
      order: 1;
      margin-top: -56px;
      width: 100%;
      .tns-controls {
        height: 56px;
        width: 50%;
        margin-left: 50%;

        button {
          height: 56px;
          width: 64px;
          background-size: 20px 12px !important;
          &[data-controls="prev"] {
            margin-left: auto;
            background-position: 32px 22px !important;
          }
          &[data-controls="next"] {
            //margin-right: 28px;!import
            background-position: 12px 22px !important;
          }
        }
      }
      .tns-inner {
        width: 100% !important;
        height: 100% !important;

        .twitter-posts,
        .twitter-post {
          height: 100% !important;
          width: 100% !important;

          .twitter-text {
            padding: 24px 16px;
          }
        }
      }
    }
  }
}
