.search-box-row {
  text-align: center;
  padding-bottom: 58px;
}

.search-box-outer {
  text-align: left;
  display: inline-block;
  margin: auto;
}

.search-box {
  .cta {
    margin-bottom: 24px;
  }
}

.search-box-form {
  display: flex;
  justify-content: space-between;
  align-items: center;

  input {
    width: 100%;
    height: 56px;
    border: 0;
    border-radius: 4px;
    box-shadow: none;
    background: $color-white;
    padding: 16px 16px 16px 56px;

    @media screen and (max-width: 768px) {
      padding: 16px;
      font-size: 12px;
    }

    &::placeholder {
      color: #545454;
    }
  }

  .search-box-input {
    margin-right: 12px;
    flex-grow: 1;
    position: relative;

    &::after {
      position: absolute;
      top: 4px;
      bottom: 4px;
      left: 4px;
      width: 48px;
      content: '';

      pointer-events: none;

      background-image: url(images/search.svg);
      background-size: 16px auto;
      background-position: center center;
      background-repeat: no-repeat;
      opacity: 0.4;

      @media screen and (max-width: 768px) {
        display: none;
      }
    }
  }

  button {
    width: 184px;
    height: 56px;
    border: 2px solid $color-white;
    border-radius: 4px;
    background: transparent;
    color: $color-white;
    text-transform: uppercase;
    font-weight: 700;

    @media screen and (max-width: 768px) {
      width: auto;
      padding-left: 16px;
      padding-right: 16px;
    }

    span {
      display: inline-block;
      padding-left: 20px;

      background-image: url(images/arrow-right.svg);
      background-size: 12px auto;
      background-position: center left;
      background-repeat: no-repeat;
    }

    &:hover {
      background-color: white;
      color: $color-black;

      span {
        background-image: url(images/arrow-right-black.svg);
      }
    }
  }
}

.search-box-filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 24px;

  @media screen and (max-width: 768px) {
    flex-wrap: wrap;
  }

  div {
    flex-grow: 1;
    margin-right: 15px;

    @media screen and (max-width: 768px) {
      width: 100%;
      margin-bottom: 16px;
    }

    @media screen and (max-height: 768px) {
      padding-right: 10px;
    }
  }

  button,
  .filter-button {
    flex: 1;
    height: 40px;
    min-width: 139px;

    border: 0;
    border-radius: 4px;
    background: #0092cb;
    color: $color-white;

    font-size: 16px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;

    &:hover,
    &.active-filter {
      background-color: #00507f;
    }

    &:not(:last-child) {
      margin-right: 10px;
    }

    @media screen and (max-width: 768px) {
      flex: calc(50% - 12px);
      min-width: 0;
      flex-grow: 0;
      margin-bottom: 8px;

      &:nth-child(2) {
        margin-right: 12px;
      }

      &:nth-child(3) {
        margin-left: 12px;
      }

      &:not(:last-child) {
        margin-right: 0;
      }
    }
  }

  .filter-button,
  .filter-button:hover {
    line-height: 38px;
    cursor: pointer;
    text-decoration: none;
  }
}

.posts-navigation {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 0 60px;
  width: 100%;
  h1, h2, h3, h4, h5, h6 {
    display: none;
  }
}

.nav-links a {
  border: 1px solid black;
}