.vacancies-hero-heading-container {
  max-width: 563px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.vacancies-cards {
  .section-card h3 {
    margin-bottom: 64px;
  }
}

.vacancies-list {
  .section-grey-bg {
    top: 160px;
    bottom: 0;
    height: auto;

    @media screen and (max-width: 768px) {
      top: auto;
      height: 160px;
    }
  }

  h2 {
    margin-bottom: 8px;
    min-height: 0;
  }

  p {
    margin-bottom: 48px;

    @media screen and (max-width: 768px) {
      margin-bottom: 40px;

      text-align: left;
    }
  }
}

.vacancies-filters {
  h2 {
    margin-bottom: 48px;

    text-align: center;
  }

  .dropdown-wrapper {
    @media screen and (max-width: 768px) {
      margin-bottom: 24px;
    }
  }
}

.vacancies-full-width {
  position: relative;

  a {
    position: relative;
    display: block;

    text-decoration: none;
    color: $color-white;
  }

  img {
    width: 100%;
    height: auto;
  }

  a > div {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;

    color: $color-white;
  }
}

.vacancies-vacancy-cards {
  padding: 72px 0 160px;

  @media screen and (max-width: 768px) {
    padding-top: 0;
    padding-bottom: 0;
  }

  .section-grey-bg {
    bottom: 0;
    height: 320px;
  }
}

.vacancies-our-culture {
  padding-bottom: 160px;

  @media screen and (max-width: 768px) {
    background: transparent;
    padding-bottom: 48px;
  }

  img {
    width: 100%;
    height: auto;
  }

  h2 {
    margin-bottom: 8px;
  }

  h3 {
    margin-bottom: 24px;
  }

  .button {
    margin-top: 48px;

    @media screen and (max-width: 768px) {
      margin-top: 24px;
    }
  }
}

.vacancies-our-culture-cta {
  @media screen and (max-width: 768px) {
    text-align: center;
    margin-bottom: 48px;
  }
}
