.archive-games {
    .single-post {
        flex-wrap: unset;
        margin: 60px auto;

        h2 {
            font-size: $heading-1;
            line-height: $heading-1;
        }
        h3{
            font-size: $heading-2;
            line-height: 3em;
        }

        img {
            width: 100%;
            height: auto;
        }

        .single-post-link-wrapper {
            margin:2em auto 3em;
            a.single-post-link {
                @include btn-black;
            }
        }

        .content-image {
            flex: 0 0 56.67%;
        }
        .content-text {
            flex: 0 0  43.33%;
        }

        &:nth-child(even) {
            flex-direction: row-reverse;

            .content-text {
                padding-left: 9.5em;
            }
        }
        &:nth-child(odd) {
            flex-direction: row;
            .content-text {
                padding-right: 9.5em;
            }
        }
    }
}