.education-hero-slideshow {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  overflow: hidden;

  img {
    object-fit: cover;
  }
}

.education-article {
  h2 {
    hyphens: none;

    @media screen and (max-width: 768px) {
      margin-bottom: 24px;
    }
  }

  h3 {
    @media screen and (max-width: 768px) {
      margin-bottom: 8px;
    }
  }
}

.education-columns {
  margin-top: 64px;
  margin-bottom: 80px;

  @media screen and (max-width: 768px) {
    margin-top: 0;
    margin-bottom: 48px;
  }
}

.education-apply-now {
  padding: 80px 0;

  background: $color-blue;
  color: $color-white;

  @media screen and (max-width: 768px) {
    padding: 48px 0;
  }

  h3 {
    margin-bottom: 48px;

    @media screen and (max-width: 768px) {
      margin-bottom: 24px;
    }
  }

  p {
    color: $color-white;
  }
}

.education-application-form {
  position: relative;

  &::before {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    height: 320px;
    content: '';

    background: $color-blue;
  }

  h3 {
    margin-bottom: 40px;
  }

  form {
    padding: 56px;

    background: $color-white;

    @media screen and (max-width: 768px) {
      padding: 22px 15px;
      margin-left: -15px;
      margin-right: -15px;
    }
  }

  .field-wrapper {
    margin-bottom: 16px;

    input {
      width: 100%;
      padding: 8px 0;

      border: 0;
      border-bottom: 1px solid #858585;

      &::placeholder {
        color: #545454;
      }
    }

    textarea {
      width: 100%;
      height: 192px;

      background: $color-light-grey;
      border: 0;
      color: #545454;

      font-size: 16px;
      line-height: 24px;

      &.short {
        height: 150px;
      }
    }

    label {
      display: block;
      margin-bottom: 16px;
      margin-top: 24px;

      color: #545454;

      font-size: 16px;
      line-height: 24px;
    }
  }

  ul {
    margin: 0 0 64px;
    padding: 0;

    list-style: none;
  }

  li {
    margin: 0 0 16px;
    display: flex;
    align-items: center;
    justify-content: flex-start;

    color: #545454;

    input {
      width: 29px !important;
      height: 32px;

      flex-grow: 0;

      background: #f0f0f0;
    }

    div {
      flex-grow: 1;
      text-align: left;
    }
  }

  .checkbox-wrapper {
    width: 32px;
    height: 32px;
    flex-grow: 0;
    flex-shrink: 0;
    margin-right: 16px;
    position: relative;

    .checkbox-element {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;

      background: #f0f0f0;
      content: '';
      pointer-events: none;

      &::after {
        position: absolute;
        top: 8px;
        right: 8px;
        bottom: 8px;
        left: 8px;

        content: '';
      }
    }

    input {
      opacity: 0;
    }
  }

  .terms-label {
    margin-bottom: 8px;

    color: $color-black;
  }

  .submit-form {
    margin-top: 48px;
    margin-bottom: 48px;

    text-align: center;
  }
}

.checkbox-wrapper input:checked ~ .checkbox-element::after {
  background: $color-blue;
}
