.footer {
    padding: 40px 0;
    color: $footer-color;
    background: $footer-bg;

    @media screen and (min-width: 768px) {
        padding: 80px 0 40px;
    }

    ul {
        display: flex;
        flex-direction: column;
        margin: 0;
        padding: 0;
        list-style: none;

        li {
            margin: 0;
            padding: 0;

            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }
    }

    p {
        color: $footer-color;
        font-size: 16px;
    }

    a {
        &:not(.button) {
            color: $footer-color;
            text-decoration: none !important;

            &:hover {
                color: $footer-color-hover;
            }
        }
    }

    &__main {
        display: flex;
        flex-direction: column;
        @media screen and (min-width: 992px) {
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__brand {
        @media screen and (min-width: 768px) {
            max-width: 60vw;
        }

        @media screen and (min-width: 992px) {
            width: 30vw;
            max-width: 400px;
            margin-right: 15%;
        }

        .logo {
            margin-bottom: 30px;
        }
    }

    &__buttons {
        .button {
            &:not(:last-child) {
                margin-right: 16px;
            }
        }
    }

    &__menus {
        flex-grow: 1;
        margin-top: 20px;

        @media screen and (min-width: 992px) {
            margin-top: 0;
        }

        a {
            font-size: 14px;
        }

        > ul {
            display: flex;
            flex-direction: column;

            @media screen and (min-width: 992px) {
                flex-direction: row;
                justify-content: space-between;
            }

            > li {
                margin-top: 20px;
                margin-bottom: 0;

                @media screen and (min-width: 992px) {
                    margin-top: 0;
                }

                > a {
                    font-weight: 600;
                    font-size: 16px;
                }

                > ul {
                    margin-top: 15px;
                }
            }
        }
    }

    &__social {
        a {
            display: flex;
            align-items: center;
            font-size: 14px;

            img {
                width: 20px;
                margin-right: 10px;
            }
        }
    }

    &__legal {
        border-top: 1px solid $color-scroll;
        margin-top: 48px;
        padding-top: 24px;
        display: flex;
        flex-direction: column-reverse;
        font-size: 14px;

        @media screen and (min-width: 992px) {
            flex-direction: row;
            justify-content: space-between;
            margin-top: 80px;
        }

        p {
            font-size: 14px;
        }

        ul {
            margin-bottom: 24px;

            @media screen and (min-width: 992px) {
                flex-direction: row;
                margin-bottom: 0;
            }

            li {
                @media screen and (min-width: 992px) {
                    &:not(:last-child) {
                        margin-right: 16px;
                    }
                }
            }

            a {
                text-decoration: underline !important;
            }
        }
    }
}

