@media screen and (min-width: 1024px) {
  .js-milestones-slideshow,
  .js-milestones-slideshow .slideshow-slide {
    min-height: 560px;
  }
}

.js-milestones-slideshow {
  position: relative;
  max-width: 1440px;
  margin: 0 auto;
  background: #f5f5f5;

  .slideshow-img {
    position: absolute;
    left: 0;
    top: 0;
    width: 50%;
    height: 100%;
    background: transparent no-repeat center;
    background-size: cover;

    img {
      position: absolute;
      width: 2px;
      height: 1px;
      opacity: 0;
    }

    @media screen and (max-width: 767px) {
      position: relative;
      width: 100%;
      height: 100vw;
      max-height: 400px;
    }
  }

  .tns-controls {
    button {
      top: 50%;

      &[data-controls="prev"] {
        left: 40px;

        @media screen and (max-width: 767px) {
          left: 16px;
        }
      }

      &[data-controls="next"] {
        right: calc(50% + 40px);

        @media screen and (max-width: 767px) {
          right: 16px;
        }
      }

      @media screen and (max-width: 767px) {
        top: 200px;
      }

      @media screen and (max-width: 400px) {
        top: 50vw;
      }
    }
  }
}

.about-milestones-slide-content {
  padding-top: 48px;
  padding-bottom: 48px;
  padding-left: 32px;

  @media screen and (max-width: 767px) {
    padding-top: 96px;
    padding-left: 0;
    padding-bottom: 40px;
  }

  h4 {
    margin-bottom: 8px;
  }

  h3 {
    margin-bottom: 24px;
  }

  p {
    margin-bottom: 48px;
  }
}

.about-milestones {
  h2 {
    margin-bottom: 88px;

    @media screen and (max-width: 767px) {
      margin-bottom: 48px;
    }
  }
}

.about-milestones-slideshow-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;

  min-height: 560px;
  background: $color-light-grey;

  .container {
    position: relative;
    height: 100%;
    flex-grow: 1;
  }
}


.js-milestones-slideshow .slideshow-pager {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  .container {
    position: relative;
    height: 100%;
  }
}

.about-milestones-timeline {
  position: absolute;
  right: 30px;
  top: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0;
  padding: 0;

  list-style: none;

  @media screen and (max-width: 767px) {
    width: auto;
    height: auto;
    left: 24px;
    right: 24px;
    top: 400px;
    bottom: auto;
    margin-top: 24px;
    flex-direction: row;
    overflow-x: auto;
    overflow-y: hidden;
    justify-content: flex-start;
  }

  @media screen and (max-width: 400px) {
    top: 100vw;
  }

  &::before {
    transform: translateX(-50%);
    flex-grow: 1;
    border-right: 1px solid $color-black;
    content: '';
    opacity: 0.4;
    margin-top: 48px;

    @media screen and (max-width: 767px) {
      margin-top: 0;

      transform: translateY(-50%);
      border-right: 0;
      border-bottom: 1px solid $color-black;
    }
  }

  &::after {
    transform: translateX(-50%);
    flex-grow: 1;
    border-right: 1px solid $color-black;
    content: '';
    opacity: 0.4;
    margin-bottom: 48px;

    @media screen and (max-width: 767px) {
      margin-bottom: 0;

      transform: translateY(-50%);
      border-right: 0;
      border-bottom: 1px solid $color-black;
    }
  }

  li {
    margin: 0;
    padding: 0;
    margin-bottom: 24px;

    @media screen and (max-width: 767px) {
      margin-bottom: 0;
      margin-right: 34px;
    }

    &:first-child {
      margin-top: 24px;

      @media screen and (max-width: 767px) {
        margin-top: 0;
        //margin-left: 24px;
      }
    }

    &.last {
      @media screen and (max-width: 767px) {
        //margin-right: 24px;
      }
    }

    &.active a::after {
      position: absolute;
      top: 50%;
      right: -8px;

      width: 4px;
      height: 4px;
      border-radius: 4px;
      content: '';
      margin-top: -2px;

      background: $color-black;

      @media screen and (max-width: 767px) {
        top: auto;
        bottom: -12px;
        right: auto;
        margin-left: -50%;
        margin-top: 0;
      }
    }
  }

  a {
    position: relative;

    color: $color-black;

    font-size: 16px;
    line-height: 24px;
  }
}

.about-milestones-slideshow {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 50%;
  overflow: hidden;
  z-index: 10;

  @media screen and (max-width: 767px) {
    position: relative;
    height: 400px;
    width: 100%;
  }

  img {
    object-fit: cover;
  }

  .slideshow-main-nav.left {
    left: 40px;

    @media screen and (max-width: 767px) {
      left: 15px;
    }
  }

  .slideshow-main-nav.right {
    right: 40px;

    @media screen and (max-width: 767px) {
      right: 15px;
    }
  }
}

.darkmode {
  .about-milestones-slide-content {
    h1, h2, h3, h4, h5, h6, p {
      color: $color-black;
    }
  }
}