/*  SECTIONS  */
.section {
  clear: both;
  padding: 0px;
  margin: 0px;
}

/*  COLUMN SETUP  */
.col {
  display: block;
  float: left;
  margin: 1% 0 1% 1.6%;
}
.col:first-child {
  margin-left: 0;
}

/* Adjust column margins */
.row {
  margin-left: -24px;
  margin-right: -24px;

  @media screen and (max-width: 767px) {
    margin-left: -12px;
    margin-right: -12px;
  }
}

.row [class^="col-"],
.row [class*=" col-"] {
  padding-left: 24px;
  padding-right: 24px;

  @media screen and (max-width: 767px) {
    padding-left: 12px;
    padding-right: 12px;
  }
}

/*  GROUPING  */
.group:before,
.group:after {
  content: '';
  display: table;
}
.group:after {
  clear: both;
}
.group {
  zoom: 1; /* For IE 6/7 */
}
