.about-hero {
  position: relative;
  background: $color-blue;

  .row {
    z-index: 10;
  }

  h2 {
    margin-bottom: 8px;
  }

  h3 {
    margin-bottom: 48px;
  }

  p {
    color: $color-white;
  }

  .container {
    justify-content: center;
  }

  .hero-scroll-down-container {
    margin-bottom: 68px;
    margin-top: 0;
  }

  .hero-slide-indicator {
    margin-top: 10px;
  }
}

.about-hero-slideshow {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 50%;
  overflow: hidden;
  background: $color-black;
  z-index: 5;

  img {
    width: 100%;
    height: auto;
    min-height: 100%;
  }

  @media screen and (max-width: 767px) {
    display: none;
  }
}

.about-hero-content-row {
  flex-grow: 1;
  flex-direction: column;
}

.about-perks {
  &.section-padding-top {
    padding-top: 124px;

    @media screen and (max-width: 768px) {
      padding-top: 68px;
    }
  }

  &.section-padding-bottom {
    padding-bottom: 88px;

    @media screen and (max-width: 768px) {
      padding-bottom: 64px;
    }
  }
}

.about-our-studios {
  padding-top: 0;
}

.about-studio-image-wrapper {
  position: relative;

  img {
    width: 100%;
    height: auto;
  }

  div {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  h3 {
    color: $color-white;
  }
}
