.about2-hero-heading {
  flex-direction: column;
  justify-content: center;

  h2 {
    margin-bottom: 8px;
  }
}

.about2-content-section {
  margin-top: 120px;

  h3 {
    margin-bottom: 24px;

    @media screen and (max-width: 768px) {
      margin-bottom: 16px;
    }
  }

  @media screen and (max-width: 768px) {
    margin-top: 48px;
  }
}

.about2-content-video-poster {
  margin-top: 40px;
  width: 100%;
  height: auto;
}

.about2-get-in-touch {
  .container {
    padding: 88px 0;

    background: $color-blue;
    color: $color-white;

    @media screen and (max-width: 768px) {
      padding: 48px;
    }

  }

  h3 {
    margin-bottom: 16px;
  }

  p,
  a {
    color: $color-white;
  }
}

.about2-get-in-touch-avatar {
  width: 100%;
  height: auto;

  border-radius: 50%;

  @media screen and (max-width: 768px) {
    width: 160px;
    margin: 48px auto 24px;
  }
}

.about2-get-in-touch-details {
  @media screen and (max-width: 768px) {
    text-align: center;
  }
}
