.community-teaser {
    padding: 80px 0;
    background: $color-purple;

    @media screen and (min-width: 768px) {
        padding-top: 100px;
        padding-bottom: 100px;
    }

    h2 {
        color: $color-scroll;
        margin-bottom: 20px;
    }

    p {
        color: $color-scroll;
    }

    &__buttons {
        padding-top: 0.5rem;

        a {
            &:not(:last-child) {
                margin-right: 16px;
            }
        }
    }

    &__image {
        border-radius: 16px;
        overflow: hidden;
        margin-top: 40px;
        height: 100%;

        @media screen and (min-width: 992px) {
            border-radius: 24px;
            margin-top: 0;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            min-height: 50vw;

            @media screen and (min-width: 992px) {
                min-height: 0;
            }
        }
    }

}
