[data-text-preview] {
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    &.transformed {
        margin: 0 0 80px;
    }
    h1, h2, h3, h4, h5, h6 {
       width: 100%;
    }
    a {
        &:hover {
            text-decoration: none !important;
        }
    }
    .button {
        &:before {
            position: absolute;
            bottom: 50px;
            left: 0;
            right: 0;
            height: 300px;
            content: '';
            background: url('images/fade.svg') repeat-x 100% 100%;
        }
    }
}