.games-specifications {
    margin: 0 0 40px;
    h1, h2, h3, h4, h5, h6 {
        margin: 0 0 60px;
        @media screen and (min-width: 768px) {
            margin-bottom: 70px;
        }
    }
    table {
        width: 100%;
    }
    thead {
        tr {
            border: none;
        }
    }
    tr {
        border: 1px solid $color-plum;
    }
    th {
        padding: 15px 10px;
        text-align: left;
        font-weight: 600;
        color: $color-scroll;
        background: $color-plum;
        &:first-child {
            background: transparent;
        }
        &:not(:first-child) {
            border-right: 1px solid $color-scroll;
        }
        &:first-child,
        &:last-child {
            border-right: 1px solid $color-plum;
        }
    }
    td {
        padding: 15px 10px;
        text-align: left;
        font-weight: normal;
        &:first-child {
            color: $color-scroll;
            background: $color-plum;
            font-weight: 600;
            border-bottom: 1px solid $color-scroll;
        }
        &:not(:first-child) {
            border-right: 1px solid $color-plum;
        }
    }
}
