.portfolio-hero {
  background-image: url(images/blue-byte-portfolio-hero-background.jpg);
}

.portfolio-featured {
  &.section-padding-top {
    padding-top: 124px;

    @media screen and (max-width: 768px) {
      padding-top: 8px;
    }
  }

  &.section-padding-bottom {
    padding-bottom: 120px;

    @media screen and (max-width: 768px) {
      padding-bottom: 0;
    }
  }
}

.portfolio-featured-game {
  position: relative;
  cursor: pointer;
  display: block;
  height: 100%;

  @media screen and (max-width: 768px) {
    margin-bottom: 8px;
    margin-left: -15px;
    margin-right: -15px;
  }

  .img-shadow {
    height: 100%;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &:hover {
    .button {
      background-color: $color-white;
      color: $color-black;
    }
  }
}

.portfolio-featured-game-text {
  padding: 56px;

  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  color: $color-white;

  @media screen and (max-width: 768px) {
    padding: 24px;
  }

  h2 {
    margin-bottom: 32px;
  }
}

.portfolio-full-width {
  position: relative;
  background: transparent none center;
  background-size: cover;
  height: 80vw;
  max-height: 80vh;

  > div {
    position: absolute;
    //top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    //align-items: center;
    justify-content: center;

    color: $color-white;

    align-items: flex-start;
    top: 56px;

    @media screen and (max-width: 768px) {
      top: 24px;
    }
  }

  a {
    display: inline-block;
    color: $color-white;
  }

  .button {
    color: $color-black;

    &:hover {
      color: $color-white;
    }
  }

  h2 {
    margin-bottom: 32px;
  }

  &.has-mobile-layout {
    @media screen and (max-width: 767px) {
      max-height: none;
      height: 177.77vw;
    }
  }

  &.portfolio-full-width-16_9 {
    height: auto;
    max-height: none;
    padding-bottom: 56.25%;

    @media screen and (max-width: 768px) {
      height: auto;
      padding-bottom: 100%;
    }
  }
}

.portfolio-full-width-wrapper {
  &.section-padding-top {
    padding-top: 120px;

    @media screen and (max-width: 767px) {
      padding-top: 24px;
    }
  }

  &.section-padding-bottom {
    padding-bottom: 120px;

    @media screen and (max-width: 767px) {
      padding-bottom: 24px;
    }
  }
}

.portfolio-games-list {
  &.section-padding-top {
    padding-top: 72px;

    @media screen and (max-width: 768px) {
      padding-top: 0;
    }
  }

  .section-item {
    display: flex;
    flex-direction: column;
    text-align: left;

    @media screen and (max-width: 768px) {
      margin-bottom: 48px;
    }

    /*
    @media screen and (min-width: 768px) {
      .button {
        position: absolute;
        bottom: -20px;
      }
    }
    */

    .img-shadow {
      flex-grow: 1;

      img {
        height: 100%;
        object-fit: cover;
      }
    }
  }

  h2 {
    @media screen and (max-width: 768px) {
      margin-bottom: 24px;
    }
  }
}

.zoom-out-teaser {
  .zoom-outer-img-container {
    overflow: hidden;

    img {
      transform: scale(1.1);
      transition: transform 0.3s ease-in-out;
      margin: 0;
    }

    .section-list & {
      margin-bottom: 24px;
    }
  }

  &:hover {
    .zoom-outer-img-container img {
      transform: scale(1);
    }
  }
}
