.header {
    padding: 0;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    height: $header-height;
    z-index: 100;
    background: $header-bg;

    transition: 0.25s ease;

    &.hidden {
        opacity: 0;
        pointer-events: none;
    }

    &.bg {
        background: $header-bg;
        padding: 16px 0;
    }

    .container,
    .row,
    .row div {
        height: 100%;
    }

    // @media screen and (max-width: 768px) {
    //   padding: 56px 24px;
    // }
}

.header-margin {
    padding: 200px 0 0;
    height: 1px;
}
