.slideshow {
  position: relative;
  height: 1000px;
  max-height: 80vh;
  overflow: hidden;

  img {
    width: 100%;
    height: auto;
  }
}

.slideshow-content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;

  color: $color-white;

  .container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 56px;
    padding-bottom: 56px;
    flex-grow: 1;

    @media screen and (max-width: 768px) {
      padding-top: 24px;
      padding-bottom: 24px;
    }
  }

  h2 {
    margin-bottom: 16px;
  }
}

.slideshow-current-image {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  z-index: -1;
  &.js-current-image {
    z-index: auto;
  }

  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;

  transition: opacity 2s ease;

  &.no-transition {
    transition-duration: 0s;
  }
}

.slideshow-nav {
  width: 100%;

  .col-2 {
    flex: 0 1 208px;
  }
}

.slideshow-item {
  position: relative;
  cursor: pointer;

  span {
    @media screen and (max-width: 768px) {
      display: none;
    }
  }

  &:hover {
    color: #0092cb;
  }

  &::before {
    position: absolute;
    top: -14px;
    left: 0;
    right: 0;
    height: 2px;
    content: '';

    background: $color-white;
  }

  &::after {
    position: absolute;
    top: -14px;
    left: 0;
    height: 2px;
    content: '';
    width: 0;

    background: #0092cb;

    .active & {
      animation: animate-slideshow-item 5s linear 1;
    }

    @keyframes animate-slideshow-item {
      from {
        width: 0;
      }

      90% {
        width: 100%;
      }

      to {
        width: 100%;
      }
    }
  }
}

.slideshow-main-nav {
  position: absolute;
  top: 50%;
  bottom: 0;
  width: 64px;
  height: 64px;
  margin-top: -32px;

  background-size: 16px auto;
  background-repeat: no-repeat;

  &.left {
    left: 40px;
    background-image: url(images/chevron-left.svg);
    background-position: left center;

    @media screen and (max-width: 1279px) {
      left: 15px;
    }
  }

  &.right {
    right: 40px;
    background-image: url(images/chevron-right.svg);
    background-position: right center;

    @media screen and (max-width: 1279px) {
      right: 15px;
    }
  }
}

.single-slideshow {
  position: relative;
}

.single-slideshow-image {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.tns-controls {
  button {
    display: block;
    width: 64px;
    height: 64px;
    background: transparent no-repeat;
    background-size: 16px auto;
    position: absolute;
    transform: translateY(-50%);
    z-index: 10;
    border: 0 none;
    text-indent: -999px;
    overflow: hidden;

    &[data-controls="prev"] {
      background-image: url(images/chevron-left.svg);
      background-position: left center;
    }

    &[data-controls="next"] {
      background-image: url(images/chevron-right.svg);
      background-position: right center;
    }
  }
}

.tns-item {
  position: relative;
}
