@mixin hide-text {
  overflow: hidden;
  text-indent: -9999px;
}

@mixin btn-black {
  background: $color-black;
  border-radius: 2px;
  color: $color-white;
  font-size: 16px;
  padding: 11px 2em;

  &:hover {
    text-decoration: none!important;

  }
}

@function encodeColor($string) {
    @if type-of($string) == 'color' {
        $hex: str-slice(ie-hex-str($string), 4);
        $string: unquote("#{$hex}");
    }
    $string: '%23' + $string;
    @return $string;
}
