.comments {
    padding-top: 20px !important;
    margin-bottom: 120px !important;

    .comment-reply-title {
        font-size: 20px;

        small {
            font-size: 16px;
            font-family: $font-copy;
            margin-left: 10px;
            text-decoration: underline;
            color: $color-purple;
            text-transform: none;
        }
    }
}

.comments-meta {
    margin: 0 0 40px;
    font-size: 18px;
}

.comments-form-column {
    @media screen and (min-width: 992px) {
        position: sticky !important;
        top: 100px;
    }
}

.comment {

    article.comment {
        margin: 0 50px 20px 0;
        padding: 10px;
        border: 1px solid $color-plum;
        border-radius: 8px;

        @media screen and (min-width: 992px) {
            padding: 20px;
        }

        @media screen and (max-width: 1200px) {
            margin-right: 20px;
        }

        &.ubisoft-comment {
            color: $color-scroll;
            background: $color-purple;
            border-color: $color-purple;

            .comment-author .ubisoft-avatar {
                border-color: $color-scroll;
            }

            .comment-footer a {
                color: $color-scroll;
            }
        }
    }

    &.depth-2 {
        article.comment,
        .comment-respond {
            margin-left: 50px;
            margin-right: 0;
            @media screen and (max-width: 1200px) {
                margin-left: 20px;
                margin-right: 0;
            }
        }
    }

    &.depth-3,
    &.depth-4,
    &.depth-5,
    &.depth-6,
    &.depth-7 {
        article.comment,
        .comment-respond {
            margin-left: 100px;
            margin-right: 0;
            @media screen and (max-width: 1200px) {
                margin-left: 40px;
                margin-right: 0;
            }
        }
    }
}

.comment-list,
.comment-list ol,
.comment-list ul {
    margin: 0 !important;
    padding: 0 !important;
    list-style: none;

    li {
        margin: 0 !important;
        padding: 0 !important;
    }
}

.comment-content {
    padding: 0 0 5px 35px;
}

.comment-author {
    display: flex;
    align-items: center;
    margin: 0 0 5px;

    .initial,
    .ubisoft-avatar {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        margin: -4px 15px 0 0;
        width: 20px;
        height: 20px;
        color: transparent;
        font-size: 0;
        border-radius: 50%;
        border: 2px solid $color-plum;
    }

    .ubisoft-avatar {
        /*
        background-image: url(images/ubisoft-black.png);
        background-position: center center;
        background-repeat: no-repeat;
        background-size: 40px 40px;
        height: 40px;
        margin: 0 15px 0 0;
        width: 40px;
        */
    }

    .author-name {
        font-family: $font-headings;
        font-size: 18px;
        font-weight: 700;
        margin-right: 20px;
    }

    .comment-date {
        margin-left: auto;
        font-size: 12px;
    }
}

.comment-reply-login {
    display: none !important;
}

.comment-footer {
    padding: 0 0 0 35px;
    font-size: 12px;

    a {
        margin: 0 30px 0 0;
        color: $color-black;
        text-decoration: underline;
    }
}

.comment-respond {
    padding: 0 0 40px;

    .comment-reply-title {
        margin: 0 0 10px;
    }

    .logged-in-as {
        margin-bottom: 30px !important;
    }

    label,
    input,
    textarea {
        width: 100%;
    }

    textarea {
        padding: 10px;
        border: 1px solid $color-plum;
        border-radius: 8px;
        background: transparent;
    }

    .submit {
        @extend .button;
        @extend .dark;
        width: auto;
    }
}
