.page-template-scroll-of-fame {
    background: #81a3a2;
    padding-top: 0;
    overflow: hidden;
}

.header--scrolloffame {
    position: absolute;
    background: none;
    height: 50px;

    @media screen and (min-width: 1024px) {
        height: 80px;
    }

    &.bg {
        background: none;
    }

    &.hidden {
        opacity: 1;
        pointer-events: all;
    }

    .col-sm-12 {
        @media screen and (max-width: 767px) {
            padding: 0;
        }
    }

    .logo {
        width: 200px;
        position: absolute;
        top: 20px;
        left: 50%;
        transform: translateX(-50%);

        @media screen and (min-width: 1200px) {
            width: 200px;
        }

        @media screen and (min-width: 1600px) {
            width: 250px;
        }
    }

    .back {
        display: inline-flex;
        align-items: center;
        font-size: 14px;
        font-weight: 600;
        color: $color-purple;
        margin-top: -100px;

        svg {
            margin-right: 5px;
        }

        &:hover {
            color: $color-plum;
            text-decoration: none;
        }

        @media screen and (min-width: 1024px) {
            font-size: 18px;
            margin-top: 0 !important;
        }
    }

    nav {
        margin-top: -100px;

        @media screen and (max-width: 1200px) {
            right: 0;
        }

        @media screen and (max-width: 767px) {
            margin-right: -10px;
            right: 15px;
        }

        @media screen and (min-width: 1024px) {
            margin-top: 0 !important;
        }



        .button {
            &:not(:last-child) {
                margin-right: 10px;
            }

            @media screen and (max-width: 767px) {
                font-size: 14px;
                padding: 8px 10px;
            }
        }
    }
}

.scrolloffame {
    display: flex;
    flex-direction: column;
    //justify-content: center;
    justify-content: flex-start;
    height: var(--vh);
    background: url(images/scroll-of-fame/Sky_Complete_seemless.webp) bottom center / cover no-repeat;

    @media screen and (max-width: 467px) {
        padding-top: 50px;
    }

    * {
        transform: translateZ(0);
    }

    &__preloader {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        transition: opacity .5s ease-in-out;

        img {
            margin-bottom: 40px;
            animation: rotation 10s linear infinite;
            max-width: 50px;
        }

        &.is-hidden {
            opacity: 0;
            pointer-events: none;
        }
    }

    &__outer {

    }

    &__inner {
        width: calc(100vw - 40px);
        margin: auto;
        opacity: 0;
        transform: scale3d(0.6, 0.6, 0.6);
        transition: opacity 1s ease-in-out, transform 2s ease-in-out;

        //position: absolute;
        //top: 50%;
        //left: 50%;
        //transform: scale3d(0.6, 0.6, 0.6) translate3d(-50%, -50%, 0);
        //transform-origin: left top;

        @media screen and (max-width: 767px) {
            transform: none !important;
        }

        &.is-visible {
            opacity: 1;
            transform: scale3d(1, 1, 1);
            //transform: scale3d(1, 1, 1) translate3d(-50%, -50%, 0);
        }

        /*
        @media screen and (min-width: 768px) {
            width: calc(100vw - 40px) !important;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
         */

        @media screen and (min-width: 768px) {
            width: 80vw !important;
            margin-left: auto !important;
            max-width: 1300px;
        }

        @media screen and (min-width: 1024px) {
            width: 70vw !important;
        }

        @media screen and (min-width: 1024px) and (max-height: 950px) {
            //width: 60vw !important;
        }

        @media screen and (min-width: 1600px) {
            //margin-top: 100px;
        }
    }

    &__roll {
        position: relative;
        z-index: 1;
        overflow: hidden;
        background-position: 0 0;
        background-size: 100%;
        background-repeat: repeat-y;
        will-change: background-position;

        &--top {
            margin-bottom: -5.3%;
            background-image: url(images/scroll-of-fame/UB23I13_Scroll_LSR_v005_Rolle_oben_fG_sprites.webp);
        }

        &--bottom {
            margin-top: -6.9%;
            background-image: url(images/scroll-of-fame/UB23I13_Scroll_LSR_v005_Rolle_unten_fG_sprites.webp);
        }

        &__image {
            display: none;
        }

        .is-iOS & {

            &--top {
                background-image: none;
            }

            &--bottom {
                background-image: none;
            }

            &__image {
                display: block;
            }
            &__placeholder {
                display: none;
            }
        }
    }

    &__paper {
        height: 0;
        overflow: hidden;
        position: relative;
        color: $color-plum;

        .no-js & {
            height: 50vh;
            overflow: auto;
        }
    }

    &__shadow {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        pointer-events: none;
        width: 100%;
        mask-image: url(images/scroll-of-fame/UB23I13_Scroll_LSR_v006_Plane_fG_Alpha.svg);
        mask-repeat: repeat-y;
        mask-position: 50% 0;
        mask-size: 100%;
    }

    &__logo {
        display: block;
        margin: 0 auto 50px;
        opacity: 0.8;
        width: 100%;

        @media screen and (min-width: 768px) {
            width: 45%;
            margin-bottom: 80px;
        }
    }

    &__intro {
        margin-bottom: 0;
        font-size: clamp(16px, -1.25rem + 6vw, 24px);
        font-family: $font-headings;
        line-height: 1.2;
        text-align: center;
        opacity: 0.8;
    }

    &__outro {
        margin-top: 50px;
        margin-bottom: 40vh;
        font-size: clamp(16px, -1.25rem + 6vw, 24px);
        font-family: $font-headings;
        line-height: 1.2;
        text-align: center;
        opacity: 0.8;

        @media screen and (min-width: 768px) {
            margin-top: 80px;
        }
    }

    &__list {
        padding: 50px 25%;
        background: url(images/scroll-of-fame/UB23I13_Scroll_LSR_v006_Plane_fG.webp) top center repeat-y;
        background-size: 100%;
    }

    &__item {
        padding: 70px 0 30px;
        text-align: center;
        opacity: 0.8;
        overflow: hidden;

        @media screen and (min-width: 1024px) {
            padding: 100px 0 30px;
        }

        &__meta {
            margin-bottom: 15px;
            font-weight: bold;
            font-size:  14px;
            line-height: 1;

            @media screen and (min-width: 1024px) {
                font-size: 16px;
            }
        }

        &__comment {
            font-family: $font-headings;
            font-size: clamp(18px, -1.25rem + 6vw, 30px);
            line-height: 1.2;
        }

        &__answer {
            font-family: $font-headings;
            font-weight: bold;
            line-height: 1;
            font-size: clamp(16px, -1.25rem + 6vw, 18px);
        }

        &__separator {
            &--01 {
                margin-bottom: 30px;
            }

            &--02 {
                margin-top: 10px;
            }

            &--03 {
                margin-top: 15px;
                margin-bottom: 30px;
            }
        }
    }

    &__replay {
        text-align: center;
        height: 0;

        .button {
            opacity: 0;
        }
    }

    &__clouds {
        display: none;

        @media screen and (min-width: 1024px) {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            display: block;

            &__cloud {
                position: absolute;
                max-width: 50vw;
                top: -100%;
                left: -100%;

                .scrollmagic-pin-spacer & {
                    animation-duration: 100s;
                    animation-timing-function: linear;
                    animation-delay: 0s;
                    animation-iteration-count: infinite;

                    &--01 {
                        animation-name: cloud-left;
                        top: 0;
                        left: -50%;
                        animation-duration: 60s;

                        @media screen and (min-width: 1024px) {
                            animation-duration: 120s;
                        }
                    }

                    &--02 {
                        animation-name: cloud-right;
                        top: 60%;
                        left: auto;
                        right: -50%;
                        animation-duration: 40s;
                        animation-delay: 5s;

                        @media screen and (min-width: 1024px) {
                            animation-duration: 80s;
                        }
                    }

                    &--03 {
                        animation-name: cloud-left;
                        top: -30%;
                        left: -50%;
                        animation-duration: 50s;
                        animation-delay: 5s;

                        @media screen and (min-width: 1024px) {
                            animation-duration: 100s;
                        }
                    }

                    &--04 {
                        animation-name: cloud-left;
                        top: 20%;
                        left: -50%;
                        animation-duration: 40s;
                        animation-delay: 12s;

                        @media screen and (min-width: 1024px) {
                            animation-duration: 80s;
                        }
                    }

                    &--05 {
                        animation-name: cloud-right;
                        top: 40%;
                        left: 100vw;
                        animation-duration: 60s;
                        animation-delay: 09s;

                        @media screen and (min-width: 1024px) {
                            animation-duration: 120s;
                        }
                    }

                    &--06 {
                        animation-name: cloud-left;
                        top: 20%;
                        left: -50%;
                        animation-duration: 50s;
                        animation-delay: 12s;

                        @media screen and (min-width: 1024px) {
                            animation-duration: 100s;
                        }
                    }

                    &--07 {
                        animation-name: cloud-right;
                        top: 40%;
                        left: auto;
                        right: -50%;
                        animation-duration: 40s;
                        animation-delay: 18s;

                        @media screen and (min-width: 1024px) {
                            animation-duration: 80s;
                        }
                    }
                }

            }
        }
    }
}

@keyframes cloud-left {
    0% {
        transform: translate3d(0, 0, 0);
    }
    100% {
        transform: translate3d(calc(100vw + 100%), 0, 0);
    }
}

@keyframes cloud-right {
    0% {
        transform: translate3d(0, 0, 0);
    }
    100% {
        transform: translate3d(calc(-100vw - 100%), 0, 0);
    }
}

@keyframes rotate {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
