.features-teaser {
    padding-bottom: 32px;
    @media screen and (min-width: 768px) {
        padding-bottom: 52px;
    }

    &__header {
        padding-top: 70px;
        padding-bottom: 60px;

        @media screen and (min-width: 768px) {
            padding-top: 80px;
            padding-bottom: 70px;
        }
    }

    &__item {
        background: $color-scroll;
        border-radius: 16px;
        overflow: hidden;
        padding: 25px;
        height: 100%;

        @media screen and (min-width: 768px) {
            padding: 30px;
            border-radius: 24px;
        }
    }

    &__image {
        position: relative;
        padding-bottom: 65%;
        border-radius: 16px;
        overflow: hidden;
        margin-bottom: 20px;

        &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            pointer-events: none;
            background: linear-gradient(144deg, rgba($color-scroll, 0) 40%, rgba($color-scroll, 1) 100%);
        }

        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        svg {
            position: absolute;
            bottom: 10px;
            right: 10px;
            z-index: 1;
            color: $color-purple;
            pointer-events: none;
        }
    }

    .col-12 {
        margin-bottom: 48px;
    }

    h3 {
        margin-bottom: 10px;
        color: $color-purple;
        font-family: $font-copy;
        text-transform: none;
    }

    p {
        color: $color-purple;
        line-height: 1.2;

        &:last-child {
            margin: 0;
        }

        @media screen and (min-width: 992px) {
            font-size: 20px;
        }
    }
}
