* {
    box-sizing: border-box;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

html,
body {
    min-height: 100%;
    margin: 0;
    padding: 0;
    background-color: $body-bg;
}

body {
    padding-top: $header-height;
}

a,
button,
input,
textarea,
select {
    &:focus,
    &:active {
        outline: none;
    }
}

img {
    max-width: 100%;
    height: auto;
}

.container {
    width: 100%;
    margin: 0 auto;
    padding: 0 30px;
    max-width: 1500px;

    @media screen and (max-width: 767px) {
        padding-left: 24px;
        padding-right: 24px;
    }
}

.vc_row .vc_row {
    padding: 0 15px;
}

.vc_row .vc_row {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    max-width: 100%;

    @media (min-width: 1200px) {
        max-width: 1200px;
    }
}

.text-columns {
    margin: 0;

    @media screen and (max-width: 768px) {
        h3.h3 {
            margin-bottom: 24px;
        }

        p {
            margin-bottom: 16px;
        }
    }
}

.cta {
    font-size: 16px;
    line-height: 24px;
    font-weight: 700;
    text-transform: uppercase;
}

.dropdown-wrapper {
    position: relative;

    &::after {
        position: absolute;
        top: 50%;
        right: 16px;
        width: 16px;
        height: 8px;
        margin-top: -4px;

        content: '';
        background-image: url(images/chevron-down-black.svg);
        background-position: center center;
        background-repeat: no-repeat;
        background-size: 16px 8px;
        pointer-events: none;
    }

    select {
        box-sizing: border-box;
        width: 100%;
        height: 40px;
        padding: 8px 48px 8px 16px;

        border: 1px solid $color-black;
        border-radius: 4px;
        background: $color-white;
        color: $color-black;

        -moz-appearance: none;
        -webkit-appearance: none;
    }
}

a {
    color: $link-color;
    &:hover {
        color: $link-color-hover;
    }
}

.site-main {
    max-width: 100vw;
    overflow: hidden;
}

::selection {
    background: #0092cb;
    color: white;
}

.section-padding-top {
    padding-top: 160px;

    @media screen and (max-width: 767px) {
        padding-top: 24px;
    }
}

.section-padding-bottom {
    padding-bottom: 160px;

    @media screen and (max-width: 767px) {
        padding-bottom: 24px;
    }
}

.wp-video {
    margin-left: auto;
    margin-right: auto;
}
