.single-post {

    .wpb-content-wrapper {
        ul, ol {
            font-size: 16px;
            line-height: 24px;
            font-weight: 400;
            padding-left: 18px;

            li {
                margin-bottom: 10px;
            }
        }

        .vc_row {
            width: 100%;
            margin: 0 auto 40px;
            padding: 0;
            max-width: 1500px;
        }

        .vc_column_container {
            > .vc_column-inner {
                padding-left: 0;
                padding-right: 0;
            }

            .wpb_column  {
                margin-bottom: 50px;

                &.vc_col-sm-12:last-child {
                    margin: 0;
                }
            }
        }
    }

    .blog-post-categories {
        li {
            color: $color-black;
            margin-bottom: 20px;

            &:after {
                color: $color-black;
            }
        }
    }

    .blog-post-meta {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 40px;

        p {
            margin: 0;
        }
    }

    &__header {
        padding-top: 70px;
        padding-bottom: 10px;

        @media screen and (min-width: 768px) {
            padding-top: 80px;
            padding-bottom: 20px;
        }
    }

    &__visual {
        margin-bottom: 70px;
        text-align: center;

        @media screen and (min-width: 768px) {
            margin-bottom: 80px;
        }
    }

    &__content {
        .vc_col-sm-1:first-child,
        .vc_col-sm-2:last-child {
            display: none;

            + .vc_col-sm-8 {
                width: 100%;
                margin: 0;
            }
        }

        .game-impressions,
        .studios-studio {
            width: 1500px;
            max-width: 100vw;
            position: relative;
            left: 50%;
            transform: translateX(-50%);
        }
    }

    #comments {
        padding-top: 70px;
        border-top: 8px solid $accent-color;

        @media screen and (min-width: 768px) {
            padding-top: 80px;
        }
    }
}
