.game-hero {

    .container {
        @media screen and (max-width: 1520px) {
            padding-left: 0;
            padding-right: 0;
        }
    }

    &__wrapper {
        display: flex;

        @media screen and (max-width: 767px) {
            flex-direction: column;
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        width: 100%;
        padding: 48px;
        text-align: center;

        @media screen and (min-width: 768px) {
            padding: 64px;
            width: 42%;
        }

        h2 {
            margin-bottom: 1rem;
        }

        p {
            color: inherit;
        }
    }

    &__buttons {
        padding-top: 0.5rem;
        a {
            margin: 8px;
        }
    }

    &__image {
        width: 100%;

        @media screen and (min-width: 768px) {
            width: 58%;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

.game-hero--anno117 {
    .game-hero__content {
        padding: 100px 30px;
        color: $color-plum;
        background: url('images/anno117-frame-gold.webp') no-repeat center / 100% 100%;

        img {
            display: none;
            max-width: 90%;
            margin: 0 auto 20px;
        }

        @media screen and (min-width: 1024px) {
            background-size: cover;

            img {
                display: block;
            }
        }

        @media screen and (min-width: 1440px) {
            padding: 128px 84px;

            img {
                max-width: 100%;
            }
        }
    }
}
