body.single {
    .related-posts-wrapper .row {
        flex-direction: column;

        > div {
            max-width: 100%;
        }
            
    }

}

.post-meta-container {
    padding-top: 60px;
    margin-bottom: 60px;
    h1, h2, h3, h4, h5, h6 {
        margin-bottom: 40px;
    }
    @media screen and (max-width: 1200px) {
      .row > div {
          min-width: 100%;
          width: 100%;
          max-width: 100%;
      }
    }
}