html,
body {
    font-family: $font-copy;
    color: black;
}

h1, h2, h3, h4, h5, h6 {
    font-family: $font-headings;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 0.5rem;
    line-height: 1.1;
}

p {
    color: inherit;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
}

.text-larger {
    font-size: 18px;
}
