.tag-cloud {
    margin-top: 24px;

    .tag-cloud-link{
        color:$color-black;
        font-size: 16px!important;
        line-height: 2em;
        text-align: center;
        border:1px solid $color-black;
        border-radius: 4px;
        padding: .5em 1em;
        margin-right: 20px;

        &:hover {
            text-decoration: none;
        }
    }

}