.js-game-slideshow-stage {
  .js-slide-link {
    font-size: 14px;
    margin-right: 20px;
    margin-top: 16px;

    &:last-child {
      margin-right: 0;
    }
  }

  .js-slide-title {
    margin-bottom: 0;
  }

  .js-slide-link-placeholder {
    display: inline-block;
    padding: 16px 0;
    width: 0;
    height: auto;
    overflow: hidden;
    font-size: 14px;
    vertical-align: middle;
    margin-top: 16px;
  }

  .slideshow-slide {
    .container {
      position: relative;
      padding-top: 164px;
      display: flex;
      flex-direction: column;
      align-content: flex-start;
      justify-content: space-between;
      min-height: 100vh;
    }

    .h1.hero-heading {
      opacity: 0;
    }
  }

  .slideshow-heading {
    position: relative;
    padding: 8px 0 150px;
  }

  .studios-games-heading {
    position: absolute;
    top: 164px;
    left: 0;
    right: 0;
    z-index: 10;
  }

  .js-game-slideshow-stage-scroll-down {
    position: absolute;
    left: 0;
    bottom: 56px;
    width: auto;
    display: block;
    margin: 0;
  }

  .slideshow-pager {
    @media screen and (max-width: 767px) {
      bottom: 80px;
    }
  }
}

.js-game-slideshow-stage,
.js-game-slideshow-stage .slideshow-slide {
  height: auto;
  max-height: none;
  min-height: 100vh;
}