.button,
.nav-links a {
    display: inline-block;
    padding: 8px 20px;
    color: $button-color;
    background: $button-bg;
    border: 1px solid $button-border;
    border-radius: 4px;
    text-decoration: none;
    text-align: center;
    font-weight: 600;

    &:hover {
        color: $button-color-hover;
        background: $button-bg-hover;
        border-color: $button-border-hover;
        text-decoration: none;

        &:not(.outline) {
            //background: $color-black;
        }
    }

    span {
        display: inline-block;
        /*
        background-image: url(images/arrow-right-black.svg);
        background-size: 12px auto;
        background-position: center left;
        background-repeat: no-repeat;
        */
    }

    &:hover span {
        // background-image: url(images/arrow-right-white.svg);
    }

    &.dark span {
        // background-image: url(images/arrow-right-black.svg);
    }

    &.outline {
        color: $button-outline-color;
        background: $button-outline-bg;
        border-color: $button-outline-border;

        &:hover {
            color: $button-outline-color-hover;
            background: $button-outline-bg-hover;
            border-color: $button-outline-border-hover;
        }
    }

    &.dark {
        color: $button-dark-color;
        background: $button-dark-bg;
        border-color: $button-dark-border;

        &:hover {
            color: $button-dark-color-hover;
            background: $button-dark-bg-hover;
            border-color: $button-dark-border-hover;
        }

        &.outline {
            color: $button-dark-outline-color;
            background: $button-dark-outline-bg;
            border-color: $button-dark-outline-border;

            &.active,
            &:hover {
                color: $button-dark-outline-color-hover;
                background: $button-dark-outline-bg-hover;
                border-color: $button-dark-outline-border-hover;
            }
        }

        /*
        &:hover:not(.outline) {
            background: $color-white;
            color: $color-black;
        }
        */
    }
}

.homepage-press {
    padding-bottom: 240px;

    background: #f5f5f5;

    h2 {
        margin: 0 0 64px;
        padding: 0;

        font-size: 64px;
        line-height: 72px;
    }

    .homepage-studios-list {
        margin-bottom: 64px;
    }
}

.homepage-press-date {
    margin: 0 0 16px;

    color: $color-blue;

    font-size: 12px;
    line-height: 24px;
    letter-spacing: 0.25px;
    font-weight: 700;
}

.homepage-press-intro {
    margin: 0;
    padding: 0;

    color: #545454;

    font-size: 16px;
    line-height: 24px;
}

.calendar-button {
    border: 2px solid $color-black;
}

.button-group {
    @media screen and (min-width: 768px) {
        display: flex;
        > div, a, button {
            &:not(:first-child) {
                margin-left: 20px;
            }
        }
    }
}
