iframe {
    max-width: 100% !important;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.embed-responsive {
    > .rll-youtube-player {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: 0;
    }

    .BorlabsCookie {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        max-width: none;

        ._brlbs-content-blocker {
            width: 100%;
            height: 100%;
        }

        ._brlbs-embed {
            height: 100%;
            margin: 0;
        }
    }
}
