.game-hero {
  .hero-slide-indicator {
    width: 160px;

    > div {
      padding-top: 14px;
    }

    &:not(:last-child) {
      margin-right: 48px;
    }
  }
}

.game-section {
  padding-top: 160px;

  &.first {
    padding-top: 80px;
  }

  &.last {
    margin-bottom: 160px;
  }

  @media screen and (max-width: 768px) {
    padding-top: 52px;
  }

  h4 {
    margin-bottom: 24px;
  }

  p {
    margin-bottom: 32px;
  }

  p.game-section-kicker {
    margin-bottom: 8px;
  }

  .button {
    margin-top: 16px;
  }
}

.game-section-header {
  margin-bottom: 80px;

  @media screen and (max-width: 768px) {
    margin-bottom: 40px;
  }
}

.game-section-cta-wrapper {
  @media screen and (max-width: 768px) {
    text-align: center;
  }
}

.game-slideshow-wrapper {
  margin-top: 80px;
  margin-bottom: 30px;
  z-index: 10;
  overflow: hidden;

  @media screen and (max-width: 768px) {
    margin-top: 48px;
    margin-left: -15px;
    margin-right: -15px;
  }

  img:not(.show) {
    opacity: 0;
  }

  .slideshow-main-nav.left {
    left: 48px;
  }

  .slideshow-main-nav.right {
    right: 48px;
  }
}

.game-slideshow {
  width: 100%;
  pointer-events: none;

  @media screen and (max-width: 768px) {
    width: calc(100% + 30px);
  }
}

.game-slide-indicator-wrapper {
  margin-bottom: 48px;

  @media screen and (max-width: 768px) {
    display: flex;
    justify-content: center;
  }
}

.game-slide-indicator {
  position: relative;
  width: 160px;
  height: 2px;

  background: $color-light-grey;

  &.active::after {
    animation: animate-slide 5s linear infinite;
  }

  &::after {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;

    content: '';
    background: #0092cb;

    @keyframes animate-slide {
      from {
        width: 0;
      }

      to {
        width: 100%;
      }
    }
  }
}

.game-creator-profile {
  position: relative;
  top: 64px;

  &.pull {
    top: -120px;
  }

  &.push {
    top: 160px;
  }

  @media screen and (max-width: 768px) {
    top: 0 !important;

    &.with-margin-top {
      margin-top: 24px;
    }
  }

  p {
    @media screen and (max-width: 768px) {
      margin-bottom: 16px;
    }
  }
}

.game-creator-image {
  margin-bottom: 8px;

  border: 8px solid #d8d8d8;

  img {
    width: 100%;
  }
}

.game-image-container {
  position: relative;
}

.game-image {
  position: absolute;
  right: 0;
  width: 130%;

  z-index: -1;

  &.left {
    left: 0;
    right: auto;
  }

  @media screen and (max-width: 768px) {
    position: static;
    width: 100%;
    z-index: 1;
    margin-bottom: 24px;
  }
}

h2.game-section-heading-with-margin {
  margin-top: 120px;

  @media screen and (max-width: 768px) {
    margin-top: 48px;
    margin-bottom: 32px;
  }
}

.game-section-video {
  width: 100%;
  height: auto;
  margin-bottom: 390px;

  @media screen and (max-width: 768px) {
    margin-bottom: 0;
  }
}

.game-creator-wide-wrapper {
  position: absolute;
  right: 15px;
  margin-top: -390px;

  width: 130%;

  @media screen and (max-width: 768px) {
    position: static;
    width: 100%;
    margin-top: 0;
  }

  p.game-creator-long-description {
    margin-top: 280px;

    @media screen and (max-width: 768px) {
      margin-top: 0;
    }
  }
}

h2.game-heading-space-bottom {
  margin-bottom: 24px;
}

.game-soundcloud-widget {
  margin-top: 180px;
  margin-bottom: 160px;

  width: 100%;
  height: auto;

  @media screen and (max-width: 768px) {
    margin-top: 24px;
    margin-bottom: 24px;
  }
}

.game-impressions {
  position: relative;
  h2 {
    margin-bottom: 80px;
  }
}

.single-post .game-slideshow-wide {
  // position: absolute;
  // top: 0;
  // left: 0;
  // margin-left: -50vw;
  // width: 100vw;
}

.game-slideshow-wide {
  position: relative;

  height: 1000px;
  max-height: 80vh;
  overflow: visible;

  @media screen and (max-width: 768px) {
    height: 300px;
  }

  .container {
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  .row {
    flex-grow: 1;
  }

  .game-slide-indicator {
    position: absolute;
    bottom: 40px;
    left: 50%;
    margin-left: -80px;

    &.active::after {
      animation-duration: attr(data-slideshow-interval ms);
    }
  }
}

.game-slideshow-16_9 {
  height: auto;
  max-height: none;
  padding-bottom: 56.25%;

  @media screen and (max-width: 768px) {
    height: auto;
    padding-bottom: 100%;
  }

  > .container {
    position: absolute;
  }
}

[data-vc-full-width] {
  padding: 0 !important;
  width: 100vw !important;
  max-width: 100vw !important;
}

.single-game {
    overflow-x: hidden;
    overflow-y: auto;

    .vc_row {
        padding-bottom: 40px;
        @media screen and (min-width: 992px) {
            padding-bottom: 60px;
        }

        &:not(:first-child) {
            padding-top: 40px;
            @media screen and (min-width: 992px) {
                padding-top: 60px;
            }
        }

        &:not(.vc_row-has-fill) {
            .posts-slider {
                margin-top: -75px;
                @media screen and (min-width: 992px) {
                    margin-top: -95px;
                }
            }
        }

        .container {
            .wpb_column {
                .vc_column-inner {
                    padding: 0;
                }
            }
        }
    }

    .wpb_video_widget {
        .wpb_wrapper iframe {
            border-radius: 16px;

            @media screen and (min-width: 992px) {
                border-radius: 24px;
            }
        }
    }
}
