.homepage-hero {
  background-image: url(images/bluebyte-homepage-hero-background.png);
}

.homepage-studios {
  @media screen and (max-width: 767px) {
    padding-left: 24px;
    padding-right: 24px;

    .container {
      padding-left: 0;
      padding-right: 0;
    }
  }

  .homepage-studio {
    position: relative;
    display: block;

    @media screen and (max-width: 768px) {
      margin-bottom: 24px;
    }

    div {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: center;

      color: $color-white;
    }
  }

  .homepage-studio-overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    opacity: 0;
    display: flex;

    background: $color-white;

    transition: opacity 0.4s ease, z-index 0.4s step-end;

    @media screen and (max-width: 768px) {
      flex-direction: column;
      left: 12px;
      right: 12px;
      background: transparent;
    }

    a {
      color: $color-white;
    }

    &.open {
      opacity: 1;
      z-index: 999;

      transition: opacity 0.4s ease, z-index 0.4s step-start;
    }
  }

  .homepage-studio-overlay-map {
    flex-grow: 1;
    height: 368px;

    background-position: center center;
    background-size: cover;

    a {
      display: block;
      height: 100%;
    }

    iframe {
      margin: 0;
      padding: 0;
      border: 0 none;
      display: block;
      width: 100%;
      height: 100%;
    }

    @media screen and (max-width: 768px) {
      flex-grow: 0;
    }
  }

  .homepage-studio-overlay-content {
    position: relative;
    width: 416px;
    height: 368px;
    padding: 48px;

    color: $color-white;

    @media screen and (max-width: 768px) {
      width: 100%;
      padding: 32px;
    }

    p {
      margin: 0;

      color: $color-white;
    }

    h2 {
      margin-bottom: 24px;
    }
  }

  .homepage-studio-overlay-favorite {
    margin-top: 32px;
  }

  .homepage-studio-overlay-close {
    width: 32px;
    height: 32px;

    position: absolute;
    top: 24px;
    right: 24px;

    background: transparent;
    background-image: url(images/times.svg);
    background-size: contain;
    background-position: center center;
    background-repeat: no-repeat;
    border: 0;
  }

  .studio-selection {
    @media screen and (max-width: 767px) {
      margin-top: 30px;
    }
  }
}

.homepage-jobs {
  margin: 164px 0 0;

  .kicker {
    margin: 16px 0;

    font-size: 24px;
    font-weight: 700;
  }

  h2 {
    margin: 0;
    padding: 0;

    font-size: 96px;
    line-height: 104px;
    font-weight: 700;
  }

  p {
    margin: 28px 0 48px;
    max-width: 264px;

    color: #545454;

    font-size: 16px;
    line-height: 24px;
  }

  .button {
    margin-bottom: 368px;
  }
}

.homepage-jobs-headline-columns {
  display: flex;
  justify-content: space-between;
  position: relative;
}

.homepage-jobs-headline-column {
  &:first-child {
    width: 43%;
    flex-shrink: 0;
  }

  &:last-child {
    flex-grow: 1;
  }
}

.homepage-games-header {
  display: flex;
  margin-bottom: 54px;

  h2 {
    width: 368px;
    margin: 0;
    padding: 0;

    font-size: 40px;
    line-height: 48px;
    font-weight: 700;

    &.larger {
      font-size: 64px;
      line-height: 72px;
    }
  }

  p {
    margin: 0;
    padding: 0;
    width: 368px;

    color: #545454;

    font-size: 16px;
    line-height: 24px;
  }
}

.homepage-games-slideshow {
  img {
    width: 100%;
    height: auto;
  }
}

.homepage-studios-list {
  display: flex;
  justify-content: space-between;

  h2 {
    margin: 0 0 24px;
    padding: 0;

    font-size: 40px;
    line-height: 48px;
  }
}

.homepage-studio {
  flex: 1;

  background: #000;

  &:nth-child(2) {
    margin-left: 48px;
    margin-right: 48px;
  }

  img {
    width: 100%;
    height: auto;

    transition: 0.25s ease;
  }

  &:hover img {
    opacity: 0.6;
  }
}

.homepage-studios-bg {
  position: absolute;
  top: 600px;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;

  background: $color-light-grey;
}

.homepage-studio-image {
  position: relative;
  margin-bottom: 51px;

  div {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;

    color: #fff;

    font-size: 40px;
    font-weight: 700;
    line-height: 48px;
  }
}
