.game-features {

    &__wrapper {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-bottom: -40px;
    }

    &__item {
        margin-bottom: 40px;
        width: 100%;
        color: $color-purple;

        @media screen and (min-width: 768px) {
            width: calc(50% - 20px);
        }
    }

    h3 {
        margin-bottom: 20px;
        color: $color-purple;
    }

    p {
        color: $color-purple;
    }

}
