.press-hero {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  background: $color-blue;
  color: $color-white;

  @media screen and (max-width: 768px) {
    display: block;
    padding-top: 160px;
    padding-bottom: 48px;
  }

  .container {
    display: flex;
    align-items: center;
    flex-grow: 1;
  }

  p {
    margin-bottom: 24px;
  }

  h2 {
    margin-bottom: 16px;
  }

  h3 {
    @media screen and (max-width: 768px) {
      margin-bottom: 60px;
    }
  }

  h4 {
    margin-bottom: 16px;
  }

  p,
  a {
    color: $color-white;
  }
}

.press-avatar {
  width: 100%;
  height: auto;
  margin-top: 40px;
}

.press-filters {
  padding: 72px 0 80px;

  @media screen and (max-width: 768px) {
    padding: 24px 0 48px;
  }

  select {
    width: 100%;
    height: 56px;
    padding: 16px 48px 16px 16px;

    border: 0;
    border-radius: 4px;
    background: $color-light-grey;
    color: $color-black;

    -moz-appearance: none;
    -webkit-appearance: none;
  }
}

.related-posts-wrapper {
  padding-bottom: 240px;

  @media screen and (max-width: 767px) {
    padding-bottom: 72px;
  }
}