.media-teaser-full-width {
  position: relative;
  width: 100vw;
  height: 56.25vw;
  max-height: 80vh;
  overflow: hidden;
}

.media-teaser-image {
  position: relative;
  width: 100%;
  height: 100%;
  background: transparent no-repeat center;
  background-size: cover;
}

.media-teaser-video {
  position: absolute;
  width: 177.78vh;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  &.video-width {
    width: 100vw !important;
    height: 56.25vw;
  }

  .jw-controls {
    max-width: 100vw;
    margin: 0 auto;
    z-index: 10;
  }

  + .container {
    position: relative;
    z-index: 2;
  }

  .jwplayer .jw-preview,
  .jw-error .jw-preview {
    background-size: cover;
  }
}