.section-list {
  img {
    width: 100%;
    height: auto;
    margin-bottom: 24px;

    @media screen and (max-width: 768px) {
      margin-bottom: 10px;
    }
  }

  h2 {
    margin-bottom: 48px;
    text-align: left;

    @media screen and (max-width: 768px) {
      min-height: 0;
      margin-bottom: 32px;
      height: auto !important;
    }
  }

  .section-item {
    @media screen and (max-width: 768px) {
      text-align: center;
      margin-bottom: 50px;
    }
  }
}

.section-cards {
  a.section-card {
    color: black;
  }

  .section-card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding: 24px;
    height: 100%;

    background: $color-white;

    &-wrapper {
      @media screen and (max-width: 768px) {
        margin-bottom: 24px;
      }
    }

    h3 {
      margin-bottom: 24px;
    }

    a:hover > span {
      text-decoration: underline;
    }

    &:hover {
      text-decoration: underline;
      span {
        text-decoration: underline;
      }
    }
  }
}

.section-grey-bg {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 160px;

  background: $color-light-grey;

  &.top {
    top: 0;
    bottom: auto;
    height: 320px;
    z-index: -1;
  }

  &.bottom {
    top: 160px;
    bottom: 0;
    height: auto;
  }

  @media screen and (max-width: 768px) {
    display: none;
  }
}

.section-grey {
  background: $color-light-grey;
}

.section-press {
  h2 {
    margin-bottom: 64px;

    @media screen and (max-width: 768px) {
      margin-bottom: 48px;
    }
  }

  .section-press-release:not(:first-child) {
    @media screen and (max-width: 768px) {
      display: none;
    }
  }

  .section-press-date {
    margin-bottom: 16px;

    color: $color-black;

    font-size: 12px;
    line-height: 24px;
    letter-spacing: 0.25px;
    font-weight: 700;
  }

  .section-press-cta-row {
    margin-top: 64px;

    @media screen and (max-width: 768px) {
      margin-top: 17px;
      text-align: center;
    }
  }
}

.section-press-release a {
  color: black;
  &:hover {
    color: black;
  }
}

.section-jobs {
  h3 {
    margin-bottom: 64px;
    font-size: 90px;
    line-height: 1.1;

    @media screen and (max-width: 768px) {
      margin-bottom: 16px;
      width: 100%;
    }

    @media screen and (max-width: 1200px) {
      font-size: 56px;
      line-height: 64px;
    }
  }

  @media screen and (max-width: 768px) {
    img.img-fluid {
      width: 100%;
    }

    a.button.dark {
      padding-left: 48px;
      padding-right: 48px;
    }
  }

  .section-jobs-intro {
    margin-top: -32px;
    margin-bottom: 48px;

    @media screen and (max-width: 768px) {
      margin-top: 0;
      margin-bottom: 40px;
    }
  }
}

.section-jobs-vacancies {
  margin-top: 100px;

  @media screen and (max-width: 768px) {
    margin-top: 32px;
  }

  h2 {
    padding: 0 0 16px;
    margin: 0;

    border-bottom: 1px solid $color-black;

    font-size: 16px;
    line-height: 24px;
    font-weight: 700;
  }

  ul {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  li {
    margin: 0;
    padding: 0;
  }

  a {
    position: relative;
    display: block;
    padding: 16px 0 16px 30px;

    border-bottom: 1px solid $color-black;
    color: #545454;

    font-size: 16px;
    line-height: 24px;
    text-decoration: none;

    &:hover,
    &:focus,
    &:active {
      text-decoration: underline;
    }

    &::before {
      position: absolute;
      left: 1px;
      top: 16px;

      content: '';
    }
  }
}

.section-jobs-panel {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 320px;
  padding: 24px;
  transform: translateY(-76px);

  background: $color-light-grey;
  color: $color-black;

  text-decoration: none;

  @media screen and (max-width: 768px) {
    transform: none;
  }

  &:hover,
  &:focus,
  &:active {
    color: $color-black;

    text-decoration: none;
  }

  h2 {
    margin-bottom: 16px;

    @media screen and (max-width: 1200px) {
      font-size: 32px;
      line-height: 40px;
    }
  }

  .homepage-job-panel-footer {
    font-size: 12px;
    line-height: 24px;
    letter-spacing: 0.25px;
    font-weight: 700;
  }
}

.homepage-jobs-video {
  max-width: 100%;
  height: auto;

  @media screen and (max-width: 768px) {
    margin-top: 64px;
    margin-bottom: 26px;
  }
}

.project-portfolio-list .section-grey-bg {
  top: 160px;
  bottom: 0;
  height: auto;
}
