.studios-features {
  .section-padding-top {
    padding-top: 124px;

    @media screen and (max-width: 767px) {
      padding-top: 71px;
    }
  }

  .section-padding-bottom {
    padding-bottom: 88px;

    @media screen and (max-width: 767px) {
      padding-bottom: 48px;
    }
  }

  @media screen and (max-width: 767px) {
    h2 {
      margin-bottom: 48px;
    }
  }
}

.studios-feature {
  margin-bottom: 32px;

  color: #545454;

  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  text-align: center;

  img {
    width: auto;
    height: 80px;
    margin-bottom: 16px;
  }
}

.studios-map {
  img {
    width: 100%;
    height: auto;
  }

  h3 {
    margin-bottom: 16px;

    @media screen and (max-width: 768px) {
      margin-bottom: 24px;
    }
  }

  .button {
    margin-top: 64px;

    @media screen and (max-width: 768px) {
      margin-top: 40px;
    }
  }
}

.studios-map-copy {
  padding-top: 80px;

  @media screen and (max-width: 768px) {
    padding-top: 8px;
  }
}

.studios-studio {
  margin-bottom: 120px;
  h2 {
    margin-bottom: 8px;
  }

  h3 {
    margin-bottom: 24px;
  }

  .button {
    margin-top: 48px;

    @media screen and (max-width: 768px) {
      margin-top: 40px;
    }
  }

  img {
    width: 100%;
    height: auto;

    @media screen and (max-width: 768px) {
      margin-bottom: 10px;
    }
  }
}

.studios-games-heading {
  margin-bottom: 60px;

  @media screen and (max-width: 1200px) {
    margin: 80px 0 10px;
  }

  @media screen and (max-width: 768px) {
    h2 {
      margin-bottom: 16px;
    }

    h2 {
      margin-bottom: 32px;
    }
  }
}
