h1 {
  font-size: 40px;
  @media screen and (max-width: 768px) {
    font-size: 32px;
  }
}

h2,
h3,
h4,
h5 {
  word-break: break-word;
}

.h1 {
  margin: 0;
  padding: 0;

  font-size: 96px;
  line-height: 104px;

  @media screen and (max-width: 768px) {
    font-size: 56px;
    line-height: 64px;
  }

  @media screen and (max-height: 768px) {
    font-size: 58px;
    line-height: 58px;
  }
}

.h2 {
  margin: 0;
  padding: 0;
  font-size: clamp(40px, -1.25rem + 8vw, 56px);

  &.smaller {
    font-size: clamp(36px, -1.25rem + 8vw, 44px);
  }
}

.h3 {
  margin: 0;
  padding: 0;
  font-size: clamp(24px, -1.25rem + 5vw, 32px);
}

.h4 {
  margin: 0;
  padding: 0;
  font-size: 24px;
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
  > a {
    color: inherit;
    &:hover {
      text-decoration: none !important;
    }
  }
}

.ext-container-simple-heading {
  padding: 200px 0 100px;
