.blog-post-header {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  padding: 20px 20px 75px 125px;
  min-height: 500px;
  height: 40vh;

  &.videos {
    min-height: 300px;
    height: auto;
  }
  @media screen and (max-width: 1200px) {
    padding: 20px 40px 40px;
  }
  > div {
    position: relative;
    max-width: 60%;
    z-index: 20;
    @media screen and (max-width: 1200px) {
      max-width: 100%;
    }
  }
  &.ext-blog-post-header-missing .blog-post-title {
    color: black;
  }
  .blog-post-header-embed {
    // position: absolute;
    // top: 0;
    // left: 0;
    // right: 0;
    // bottom: 0;
    // width: 100%;
    // min-width: 100%;
    // max-width: 100%;
    // z-index: 10;
    // > * {
    //   width: 100%;
    //   height: 100%;
    // }
    position: relative;
    padding-bottom: 56.25%;
    padding-top: 0;
    height: 0;
    overflow: hidden;

    iframe,
    object,
    embed {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  @media (orientation: landscape) {
    background-size: cover !important;
  }
}

.blog-post-title {
  color: white;
  font-size: 64px;
  letter-spacing: 0;
  line-height: 72px;
  @media screen and (max-width: 600px) {
    font-size: 48px;
    line-height: 52px;
  }
  &.ext-blog-post-header-missing {
    color: black;
  }
}

.blog-post-categories {
  display: flex;
  flex-direction: row;
  margin: 0 !important;
  padding: 0 !important;
  list-style: none;
  li {
    position: relative;
    margin: 0 20px 5px 0;
    color: white;
    &:after {
      position: absolute;
      top: 0;
      right: -10px;
      content: "|";
      color: white;
    }
    &:last-child {
      &:after {
        display: none;
      }
    }
  }
  a {
    color: white;
  }
}

body:not(.darkmode) {
  .blog-post-about {
    .blog-post-title,
    .blog-post-categories li,
    .blog-post-categories li:after {
      color: $color-black;
    }
  }
}
