.js-game-slideshow,
.js-game-slideshow .slideshow-slide {
    height: 560px;
    max-height: 80vh;
    min-height: 500px;
}

.js-game-slideshow {
    display: block;
    position: relative;
    width: 100vw;
    color: $color-scroll;

    &:after {
        content: '';
        display: block;
        height: 8px;
        background: $accent-color;
    }

    .slideshow {
        position: relative;
    }

    .slideshow-slide {
        overflow: hidden;
    }

    .slideshow-heading {
        @media screen and (min-width: 992px) {
            max-width: 50%;
        }
    }

    .slideshow-img {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: transparent no-repeat center;
        background-size: cover;

        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient(90deg, rgba($color-plum, 1) 10%, rgba($color-plum, 0) 80%);
            pointer-events: none;
        }
    }

    .tns-lazy-img {
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
        width: 2px;
        height: 1px;
    }

    .js-slide-title {
        margin: 56px 0 16px;

        @media screen and (max-width: 767px) {
            margin-top: 24px;
        }
    }

    .js-slide-copy {
        display: none;

        &:not(.color-scroll):not(.color-stone):not(.color-purple):not(.color-plum) {
            color: $color-scroll;
        }

        @media screen and (min-width: 992px) {
            font-size: 18px;
            display: block;
            margin-bottom: 30px;
        }
    }

    .tns-controls {
        button {
            top: 50%;

            &[data-controls="prev"] {
                left: 40px;

                @media screen and (max-width: 1279px) {
                    left: 16px;
                }
            }

            &[data-controls="next"] {
                right: 40px;

                @media screen and (max-width: 1279px) {
                    right: 16px;
                }
            }
        }
    }

    .tns-liveregion {
        display: none;
    }

    .slideshow-pager {
        position: absolute;
        left: 0;
        bottom: 20px;
        width: 100%;

        .col-2 {
            flex: 0 1 208px;
        }
    }

    .slideshow-pager-item {
        position: relative;
        cursor: pointer;
        font-size: 14px;
        line-height: 1;

        span {
            @media screen and (max-width: 1200px) {
                display: none;
            }
        }

        &:hover {
            color: $color-purple;
        }

        &::before {
            position: absolute;
            top: -14px;
            left: 0;
            right: 0;
            height: 2px;
            content: '';

            background: $color-scroll;
        }

        &::after {
            position: absolute;
            top: -14px;
            left: 0;
            height: 2px;
            content: '';
            width: 0;
            background: $color-purple;
        }
    }

    .active .slideshow-pager-item:after {
        animation: animate-slideshow-slide 5s linear 1;
    }

    @keyframes animate-slideshow-slide {
        from {
            width: 0;
        }

        95% {
            width: 100%;
        }

        to {
            width: 100%;
        }
    }
}

.no_spacing_bottom {
    .js-game-slideshow {
        margin-bottom: 0 !important;
    }
}

@media screen and (max-width: 1200px) {
    .js-game-slideshow {
        margin-bottom: 0 !important;
    }
}
