.BorlabsCookie {
    color: #000;

    ._brlbs-caption {
        p {
            color: white;
        }
        label {
            input {
                width: 16px;
                height: 16px;
                padding: 0;
                appearance: auto;
            }
        }
    }
    .borlabs-cookie-preference {
        text-decoration: underline;
    }

    ._brlbs-embed {
        width: 100%;
        background: #ccc;

        ._brlbs-thumbnail {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &._brlbs-video-twitch {
            padding-bottom: 50%;
        }
    }
}

#BorlabsCookieBox {
    ._brlbs-box-wrap {
        @media screen and (min-width: 768px) {
            max-width: 700px !important;
        }
    }

    .cookie-preference {
        .cookie-logo {
            max-width: 80px !important;
        }
    }

    ._brlbs-btn {
        border: 1px solid #000 !important;

        &:hover {
            text-decoration: none !important;
        }
    }

    ._brlbs-btn-accept-all {
        border: none !important;
    }

    ._brlbs-legal {
        ._brlbs-cursor,
        ._brlbs-cursor + ._brlbs-separator {
            display: none !important;
        }
    }
}