.wpml-ls-legacy-dropdown-click {
    display: flex;
    align-items: center;
    height: auto !important;
    width: auto !important;
    border: 1px solid $button-border;
    border-radius: 4px;

    &:hover {
        border-color: $header-color-hover;
    }

    .wpml-ls-native {
        display: none;
    }

    > ul {
        padding: 0 !important;
    }

    a {
        color: $header-color !important;
        padding: 8px 30px 8px 8px !important;
        background: transparent !important;
        border: none !important;

        &:hover {
            color: $header-color-hover !important;
        }
    }

    a.wpml-ls-item-toggle {
        &:after {
            border: 0 !important;
            width: 14px;
            height: 8px;
            background-image: url("data:image/svg+xml,%3Csvg width='14' height='8' viewBox='0 0 14 8' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill='" + encodeColor($header-color) + "' fill-rule='evenodd' clip-rule='evenodd' d='M7.39819 7.66292C7.17851 7.88259 6.82241 7.88259 6.60274 7.66292L0.867876 1.92804C0.648208 1.70837 0.648208 1.35227 0.867876 1.13259L1.13305 0.867393C1.35271 0.647718 1.70887 0.647718 1.92854 0.867393L7.00046 5.93934L12.0724 0.867393C12.2921 0.647718 12.6482 0.647718 12.8679 0.867393L13.1331 1.13259C13.3527 1.35227 13.3527 1.70837 13.1331 1.92804L7.39819 7.66292Z'/%3E%3C/svg%3E");
        }

        &:hover {
            &:after {
                background-image: url("data:image/svg+xml,%3Csvg width='14' height='8' viewBox='0 0 14 8' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill='" + encodeColor($header-color-hover) + "' fill-rule='evenodd' clip-rule='evenodd' d='M7.39819 7.66292C7.17851 7.88259 6.82241 7.88259 6.60274 7.66292L0.867876 1.92804C0.648208 1.70837 0.648208 1.35227 0.867876 1.13259L1.13305 0.867393C1.35271 0.647718 1.70887 0.647718 1.92854 0.867393L7.00046 5.93934L12.0724 0.867393C12.2921 0.647718 12.6482 0.647718 12.8679 0.867393L13.1331 1.13259C13.3527 1.35227 13.3527 1.70837 13.1331 1.92804L7.39819 7.66292Z'/%3E%3C/svg%3E");
            }
        }
    }

    .wpml-ls-sub-menu {
        border: 1px solid $button-border !important;
        border-radius: 0 0 4px 4px;
        background: $header-bg;
        width: calc(100% + 2px);
        margin-left: -1px !important;

        &:hover {
            border-color: $header-color-hover !important;
        }
    }
}

.wpml-ls-statics-footer {
    position: relative;
    margin-bottom: 0 !important;
    ul {
        position: relative;
        top: auto !important;
        border: 1px solid $button-outline-border;
        border-radius: 5px;
        display: flex;
        background-color: $header-bg;
    }
    li {
        padding: 0 10px;
        border-bottom: 0px !important;
        background-color: $header-bg;
        border-radius: 5px;

        a span {
            color: $header-color;
            margin: auto;
        }
        &.wpml-ls-current-language {
            background: $button-bg !important;
            a span {
                color: $header-bg;
            }
        }
    }
    &.closed,
    &.open {
        ul {
            position: absolute;
            top: 0 !important;
            right: 0;
            height: auto;
            border: 1px solid white;
            border-radius: 5px;
            &:after {
                position: absolute;
                top: 15px;
                right: 8px;
                width: 16px;
                height: 8px;
                content: "";
                background: url("images/chevron-up.svg") no-repeat;
                background-size: 100% 100%;
            }
        }
        li {
            padding: 0 40px 0 10px !important;
        }
        a {
            padding: 0 !important;
        }
    }
    &.closed {
        ul {
            height: 41px;
            overflow: hidden;
            &:after {
                background: url("images/chevron-down.svg") no-repeat;
                background-size: 100% 100%;
            }
        }
    }
    li {
        display: flex !important;
        justify-content: flex-start;
        align-items: flex-start;
        margin: 0 !important;
        width: 100%;
        height: 40px;
        &:not(:last-child) {
            border-bottom: 1px solid white;
        }
    }
    a {
        display: flex !important;
        justify-content: flex-start;
        align-items: center;
        margin: 0 !important;
        padding: 0 10px;
        width: 100%;
        height: 100%;
        font-style: normal;
        background-color: transparent !important;
        &:hover {
            color: white;
        }
    }
}
