.archive-wrapper {
  padding: 56px 0 240px;

  .dropdown-wrapper select {
    //background: $color-light-grey;
  }

  @media screen and (max-width: 767px) {
    padding: 8px 0 72px;
  }
}

.post-listing {
  padding-top: 28px;

  @media screen and (max-width: 767px) {
    padding-top: 24px;
  }
}

.post-listing-row {
  position: relative;

  &.loading {
    &:before {
      display: block;
      content: "";
      position: absolute;
      left: 50%;
      bottom: 0;
      width: 60px;
      height: 60px;
      background: transparent url(images/loader.svg) no-repeat center;
      background-size: contain;
      margin: 0 0 0 -30px;
      z-index: 3;
      animation: rotation 1.4s linear infinite;
    }

    &:after {
      display: block;
      content: "";
      width: 100%;
      height: 108px;
    }
  }
}

@keyframes rotation {
  0% {
    transform: rotate(0);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.post-listing-item {
  display: block;
  margin-top: 48px;
  background-color: $color-light-grey;
  padding: 1.5em;
  margin-bottom: 3em;

  .post-listing-footer {
    display: flex;
    justify-content: flex-start;
    color: $color-darker-grey;
    font-size: 0.75em;
    line-height: 1.5em;
    margin: 1.5em 0 0;

    a,
    time {
      color: $color-darker-grey;
      font-weight: normal;
    }

    .post-listing-pipe {
      height: 1.5em;
      border-left: 1px solid $color-black;
      margin: 0 1em;
    }
    .post-listing-arrow {
      margin-left: auto;
    }

    .post-listing-categories {
    }
  }

  &.post-event {
    background-size: cover;

    .post-listing-excerpt {
      display: none;
    }
  }

  @media screen and (max-width: 767px) {
    margin-top: 24px;
  }
}

.post-listing-date {
  font-size: 12px;
  font-weight: 700;
}

.post-listing-excerpt {
  p {
    margin: 16px auto 0;
  }
}
.post-listing-title {
  font-size: 1.5em;
  font-weight: bold;
  letter-spacing: 0;
  line-height: 2em;
}
.post-listing-image img {
  width: 100%;
  object-fit: contain;
}

.post-filter-wrapper {
  .dropdown-wrapper,
  .daterangepicker-wrapper {
    margin-top: 16px;
  }
}

.archive-more-row {
  margin-top: 112px;

  .button {
    padding-left: 45px;
    padding-right: 45px;
  }

  @media screen and (max-width: 767px) {
    margin-top: 48px;
  }
}

// Post list within blog post "related articles" context
.related-posts-wrapper {
  padding: 20px 0 0 !important;
  .row {
    a {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin: 0 0 20px !important;
      padding: 0 !important;
      height: 112px;
      color: #2d2d2d;
      background: none;
      overflow: hidden;
    }
  }
  ul {
    display: flex;
    margin: 0 !important;
    padding: 0 !important;
    list-style: none;
  }
  .post-listing-row {
    margin: 0 !important;
  }
  .archive-more-row {
    display: none !important;
  }
  .post-listing-image {
    width: 30%;
  }
  .post-listing-title {
    font-size: 16px;
    font-weight: normal;
    color: #2d2d2d;
    letter-spacing: 0;
    line-height: 24px;
  }
  .post-listing-copy {
    padding: 0 0 0 20px;
    width: 70%;
  }
  .post-listing-meta {
    display: flex;
    align-items: center;
    font-size: 12px;
    font-weight: normal;
    li {
      margin: 0 0 0 5px;
      font-size: 12px;
      color: #2d2d2d;
    }
  }
  .post-listing-date {
    color: #2d2d2d;
    font-size: 12px;
    font-weight: normal;
  }
  .meta {
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    font-family: Helvetica;
    font-size: 12px;
    padding-top: 10px;
    color: #666666;
    letter-spacing: 0.25px;
    line-height: 24px;
    @media screen and (max-width: 800px) {
      flex-direction: column;
    }
    span {
      position: relative;
      padding: 2px 0 2px 20px;
      em {
        font-style: normal;
      }
      &:after {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        width: 1px;
        content: "";
        background-color: black;
      }
      &:first-child {
        padding: 2px 20px 2px 0;
      }
      &:last-child {
        &:after {
          display: none;
        }
      }
    }
    @media screen and (max-width: 800px) {
      span {
        padding: 2px 0 2px;
        &:after {
          display: none;
        }

        &:nth-child(2) {
          display: none;
        }
      }
    }
  }
}
