.sharing {
    > ul {
        display: flex;
        list-style: none;
        margin: 0;
        padding: 0;

        > li {
            margin: 0 5px !important;
        }
    }

    &__button {
        padding: 0;
        margin: 0;
        border: none;
        background: none;
        color: $color-plum;

        &:hover {
            color: $color-purple;
        }
    }
}
