.hero {
    display: block;
    min-height: 100vh;
    position: relative;

    background-color: $color-black;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    color: #fff;
    overflow: hidden;

    transition: clip-path 1s ease-out 0.3s;
    transform: translate3d(0, 0, 0);

    .container {
        display: flex;
        min-height: 100vh;
        padding-top: 164px;
        flex-direction: column;
        align-content: flex-start;
        justify-content: space-between;

        @media screen and (max-width: 767px) {
            padding-top: 154px;
        }
    }

    .button {
        min-width: 236px;
    }

    /**
    Background Video
     */
    .header-video-background {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);

        .jwplayer.jw-flag-aspect-mode {
            height: 100% !important;
        }

        .jw-wrapper {
            background: transparent none;
        }

        .jw-controls {
            max-width: 100vw;
            margin: 0 auto;
            z-index: 10;
        }

        + .container {
            position: relative;
            z-index: 2;
        }
    }
}

.hero-heading-container {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    justify-content: center;
}

.h1.hero-heading {
    font-size: 8vw;
    line-height: 1.2;
    max-width: 75%;

    @media screen and (min-width: 1440px) {
        font-size: 80px;
    }

    @media screen and (max-height: 768px) {
        font-size: 11vh;
    }

    @media screen and (max-width: 600px) {
        font-size: 48px;
        max-width: 100%;
    }
}

.hero-footer {
    padding-top: 68px;
    padding-bottom: 68px;

    @media screen and (max-width: 767px) {
        padding-top: 58px;
        padding-bottom: 58px;
    }
}

.hero-cta-row {
    display: flex;
    align-items: center;
    margin-bottom: 72px;

    a:not(:last-child) {
        margin-right: 24px;
    }

    @media screen and (max-width: 768px) {
        flex-direction: column;
        align-items: flex-start;

        a:not(:last-child) {
            margin-right: 0;
            margin-bottom: 16px;
        }
    }
}

.hero-scroll-down {
    color: $color-white;

    font-size: 16px;
    font-weight: 700;
    text-transform: uppercase;
    white-space: nowrap;

    cursor: pointer;

    &:hover span {
        text-decoration: underline;
    }

    span {
        display: inline-block;
        padding-left: 20px;

        background-image: url(images/arrow-down.svg);
        background-size: 10px auto;
        background-position: center left;
        background-repeat: no-repeat;
    }
}

.hero-scroll-down-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 72px;
}

.hero-slide-indicator-wrapper {
    margin-bottom: 48px;
}

.hero-slide-indicator {
    position: relative;
    width: 160px;
    height: 2px;

    background: $color-white;

    &.active::after {
        animation: animate-slide 5s linear infinite;
    }

    &::after {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;

        content: '';
        background: #0092cb;

        @keyframes animate-slide {
            from {
                width: 0;
            }

            to {
                width: 100%;
            }
        }
    }
}
