.simple-image {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    &.center {
        justify-content: center;
        align-items: center;
    }
    &.right {
        justify-content: flex-end;
        align-items: flex-end;
    }

    img {
        height: auto;
        max-width: 100%;
    }
}