.live-video {
    margin: 0 0 60px;
    .row {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }
    h1, h2, h3, h4, h5, h6 {
        margin-bottom: 20px;
    }
    li, a {
        font-size: 12px;
        color: #666666;
        letter-spacing: 0.25px;
        line-height: 24px;
        &:after {
            color: #666666;
        }
    }
    img {
        margin-bottom: 20px;
    }
}

[data-video-embed] {
    height: 440px;
    > * {
        width: 100%;
        height: 440px;
    }
}