.modal-video {

    .close {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 1000;
        border-radius: 50%;
        color: #5F032E;
        background: #D4B99B;
        padding: 10px;
        transform: translate(50%, -50%);
        border: 0;

        &:hover {
            color: $color-plum;
        }
    }

    .modal-content {
        background: $accent-color;
    }

    .modal-body {
        padding: 8px;
    }

}
