.imprint-hero {
  padding: 200px 0 224px;

  @media screen and (max-width: 768px) {
    padding: 160px 0 192px;
  }

  h2 {
    margin-bottom: 16px;
  }

  .h1 {
    .title-size-m {
      font-size: 80%;
    }

    .title-size-s {
      font-size: 60%;
    }
  }
}

.imprint-addresses {
  padding-bottom: 168px;

  @media screen and (max-width: 768px) {
    padding-bottom: 88px;
  }

  .cta {
    margin-bottom: 24px;
  }

  span {
    display: inline-block;
    width: 26px;
  }

  a {
    color: #545454;
  }
}

.imprint-address-column {
  @media screen and (max-width: 768px) {
    margin-bottom: 48px;
  }
}

.imprint-content {
  padding-bottom: 192px;

  @media screen and (max-width: 768px) {
    padding-bottom: 32px;
  }

  .cta {
    margin-bottom: 24px;
  }

  p {
    margin-bottom: 48px;
  }

  a {
    color: #545454;
  }
}
