.article-hero {
  padding: 200px 0 48px;

  @media screen and (max-width: 768px) {
    padding: 160px 0 32px;
  }

  h2 {
    margin-bottom: 16px;
  }
}

.aricle-body {
  margin-bottom: 160px;

  @media screen and (max-width: 768px) {
    margin-bottom: 80px;
  }
}

.article-cards {
  @media screen and (max-width: 768px) {
    margin-bottom: 24px;
  }
}

.article-card-wrapper {
  margin-bottom: 24px;
}

.article-card {
  display: block;

  padding: 16px 16px 48px;
  height: 100%;

  background: $color-light-grey;

  text-decoration: none;

  &:hover {
    text-decoration: none;
  }
}

.article-date {
  color: $color-black;

  font-size: 12px;
  font-weight: 700;
  letter-spacing: 0.25px;
  line-height: 24px;
}

.article-load-more-wrapper {
  margin-bottom: 240px;

  text-align: center;

  @media screen and (max-width: 768px) {
    margin-bottom: 80px;
  }
}
