nav {
    position: relative;
    display: flex;
    @media screen and (max-width: 1200px) {
        position: absolute;
        right: 25px;
    }

    @media screen and (max-width: 782px) {
        right: 12px;
    }

    .nav-side {
        display: flex;
        padding: 0 0 0 30px;
        height: 48px !important;
        align-items: center;
        @media screen and (max-width: 1200px) {
            display: none;
        }

        .button {
            margin: 0 15px 0 0;
        }
    }
}

.nav-search {
    position: relative;
    display: flex;
    margin: 0 0 0 10px;
    padding: 0 0 0 20px;
    justify-content: center;
    align-items: center;
    overflow: hidden;

    &.open {
        overflow: visible;

        input {
            width: 300px;
        }
    }

    input {
        position: absolute;
        right: 40px;
        margin: 0 10px 0 0;
        width: 0;
        background: white;
        border: 1px solid black;
        border-width: 0 0 1px 0;
        transition: all 0.3s;
        padding: 8px 20px;
    }

    button {
        width: 24px;
        height: 24px;
        border: none;
        padding: 0;
        background: none;
        color: $header-color;

        &:hover {
            color: $header-color-hover;
        }
    }

    span {
        position: absolute;
        display: inline-flex;
        align-items: center;
        cursor: pointer;
        color: $header-color;

        svg {
            margin-right: 5px;
        }

        &:hover {
            color: $header-color-hover;
        }
    }

    .menu-menu-container + & {
        @media screen and (max-width: 1200px) {
            display: none;
        }
    }

    .menu-item-wrapper {
        display: flex;
        align-items: center;
    }
}

.nav,
header .menu ul,
header .menu-menu-container ul,
header .menu-menu-en-container ul,
header .menu-menu-de-container ul,
header .menu-mainnavigation-container ul,
header .menu-main-navigation-container ul {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    list-style: none;

    @media screen and (max-width: 1200px) {
        display: none;
    }

    li {
        color: $header-color;

        &:hover {
            color: $header-color-hover;

            > a {
                color: $header-color-hover;
            }
        }

        &:not(:first-child) {
            margin-left: 24px;
        }
    }

    a {
        color: $header-color;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;

        &:hover {
            text-decoration: none !important;
            color: $header-color-hover;
        }
    }

    .menu-item-arrow {
        margin-left: 5px;
    }
}

nav .menu .menu-item.menu-item-has-children {
    ul.sub-menu {
        background: $header-bg;
        display: flex;
        flex-direction: column;
        margin-left: -24px;
        width: 300px;

        li {
            width: 100%;
            padding: 5px 24px;
            margin: 0px !important;
        }
    }
}

.hamburger-toggle {
    display: none;
    width: 40px;
    height: 32px;
    position: relative;

    border: 0;
    background: transparent;

    @media screen and (max-width: 1200px) {
        display: block;
    }

    span {
        position: absolute;
        right: 0;
        left: 0;
        height: 0;

        transition: 0.25s ease;

        &:first-child {
            top: 0;
            border-bottom: 2px solid $header-color;
        }

        &:nth-child(2) {
            top: 50%;
            margin-top: -1px;
            border-top: 2px solid $header-color;
        }

        &:nth-child(3) {
            top: 50%;
            margin-top: -1px;
            border-top: 2px solid $header-color;
        }

        &:last-child {
            bottom: 0;
            border-bottom: 2px solid $header-color;
        }
    }

    &.open {
        span {
            &:first-child,
            &:last-child {
                opacity: 0;
            }

            &:nth-child(2) {
                transform: rotate(45deg);
            }

            &:nth-child(3) {
                transform: rotate(-45deg);
            }

            &:first-child {
                top: 0;
                border-bottom: 2px solid $header-color;

                /*
                .inverted & {
                    border-bottom-color: $header-color;
                }
                 */
            }

            &:nth-child(2),
            &:nth-child(3) {
                border-top: 2px solid $header-color;
            }
        }
    }
}

.mobile-nav {
    -webkit-overflow-scrolling: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    z-index: -1;
    opacity: 0;
    pointer-events: none;
    overflow-y: auto;

    background: $header-bg;

    &.open {
        z-index: 9999;
        opacity: 1;
        pointer-events: all;
    }

    .icon-down {
        display: block;
        position: absolute;
        top: 50%;
        right: 16px;
        width: 16px;
        height: 8px;
        margin-top: -4px;

        pointer-events: none;
        background-image: url(images/chevron-down.svg);
        background-position: center center;
        background-repeat: no-repeat;
        background-size: contain;
    }

    .nav-search {
        margin-bottom: 40px;
        margin-left: 0;
        padding: 0 24px;

        input {
            position: static;
            width: 100%;
            height: 45px;
            margin-right: 0;
            padding: 0 10px;
        }

        button {
            position: absolute;
            right: 35px;
        }
    }
}

.mobile-nav-meta {
    > div {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 15px 0 0;
        width: 100%;
    }

    .button {
        margin: 0 10px;
    }
}

.header.bg + .mobile-nav {
    .mobile-nav-heading {
        padding-top: 16px;
        padding-bottom: 16px;
    }
}

.mobile-nav-heading {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: $header-height;
    padding: 0 27px;
}

.mobile-nav-main {
    .menu {
        margin: 0;
        padding: 32px 24px;

        list-style: none;

        > li {
            display: block;
            width: 100%;
            position: relative;

            .sub-menu {
                margin: 0;
                transition: 0.25s ease;
            }

            ul {
                margin: 16px 0;
                padding: 0;

                list-style: none;

                li > a {
                    padding-left: 30px;
                }
            }

            a {
                display: block;
                padding: 8px 0;

                border-bottom: 1px solid $header-color;
                color: $header-color;

                font-size: 28px;
                font-weight: 700;
                line-height: 1.5;
                text-decoration: none;
            }
        }
    }

    .menu-item-toggle {
        position: absolute;
        top: 8px;
        right: 0;
        width: 30px;
        height: 42px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: $header-color;
    }

    .sub-menu {
        display: none;

        &.open {
            display: block;
        }
    }
}

