@media screen and (min-width: 769px) {
    .menu-main-navigation-container menu .menu-mainnavigation-container .menu,
    .menu-menu-container .menu,
    .menu-menu-en-container .menu,
    .menu-menu-de-container .menu {
        > li {
            position: relative;
            top: 12px;
            padding-bottom: 24px;

            > ul.sub-menu {
                position: absolute;
                top: calc(100% + 2px);
                visibility: hidden;
                z-index: 1000;
                padding: 24px 0;
                border-top: 1px solid $header-color;

                li a {
                    color: $header-color;
                    white-space: nowrap;
                    &:hover {
                        color: $header-color-hover;
                    }
                }
            }
        }
    }
}
