.video-stage {
    position: relative;
    min-height: 500px;
    max-height: calc(95vh - $header-height);
    overflow: hidden;

    @media screen and (min-width: 768px) {
        min-height: 800px;
    }

    &:after {
        content: '';
        display: block;
        width: 100%;
        height: 8px;
        position: absolute;
        bottom: 0;
        left: 0;
        background: $accent-color;
    }

    &__background {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: bottom center;
        filter: blur(4px);
        opacity: 0.52;
    }

    &__content {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding-top: 40px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        background-position: bottom center;
        background-repeat: no-repeat;
        background-size: cover;

        @media screen and (min-width: 768px) {
            padding-top: 5%;
        }
    }

    &__headline {
        width: 75vw;
        min-width: 300px;
        max-width: 980px;
        margin-bottom: 50px;

        @media screen and (min-width: 768px) {
            width: 70vw;
        }

        @media screen and (min-width: 1024px) {
            width: 45vw;
        }
    }

    &__button {
        max-width: 40vw;
    }
}
