.section-post-filter-container {
    margin: 0 0 60px;
}

.section-post-filter {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;

    form {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-start;
        padding: 25px;
        width: 100%;

        .select-wrapper {
            margin: 0 20px 0 0;
        }

        @media screen and (max-width: 1200px) {
            padding-left: 10px;
            padding-right: 10px;
            > div {
                margin: 0 0 10px !important;
                width: 100%;
            }
        }
    }

    .toggle-filter {
        margin: 0 10px 10px 0;
        -moz-appearance: none;
        -webkit-appearance: none;
        cursor: pointer;
    }

    .filter-tags {
        display: flex;
        flex-wrap: wrap;
        margin: 0;
        padding: 20px 0;
        width: 100%;
        list-style: none;
        // height: 75px;
        height: 78px;
        overflow: hidden;

        &.expanded {
            overflow: visible;
            height: auto;
        }

        > li {
            display: flex;
            justify-content: center;
            align-items: center;
            // margin: 0 10px 10px 0;
            margin: 0 18px 18px 0;
            //padding: 0 15px;
            //height: 45px;
            //border-radius: 4px;
            //border: 1px solid black;
            -moz-appearance: none;
            -webkit-appearance: none;
            cursor: pointer;
        }
    }

    .select-wrapper {
        position: relative;

        &:after {
            position: absolute;
            top: 50%;
            right: 16px;
            width: 16px;
            height: 8px;
            margin-top: -4px;
            content: "";
            background-image: url("images/chevron-down-purple.svg");
            background-position: 50%;
            background-repeat: no-repeat;
            background-size: 16px 8px;
            pointer-events: none;
        }

        &:hover {
            &:after {
                background-image: url("images/chevron-down-stone.svg");
            }
        }
    }

    select {
        width: 100%;
        height: 45px;
        padding: 10px 48px 10px 16px;
        border: 1px solid $color-purple;
        border-radius: 4px;
        background: transparent;
        font-weight: 600;
        color: $color-purple;
        cursor: pointer;
        -moz-appearance: none;
        -webkit-appearance: none;

        &:hover {
            color: $color-stone;
            background: $color-purple;
            border-color: $color-purple;
        }
    }

    input {
        padding: 0 15px;
        height: 100%;
        border-radius: 4px;
        border: 1px solid black;
        -moz-appearance: none;
        -webkit-appearance: none;
        @media screen and (max-width: 1200px) {
            width: 100%;
        }
    }
}

span[data-markup] {
    display: none;
}

.section-post-filter-item {
    display: flex;
    flex-direction: column;
    position: relative;
    margin: 0 0 40px;
    width: 30%;
    min-height: 400px;
    overflow: hidden;
    border-radius: 16px;
    border: 1px solid $color-plum;

    @media screen and (max-width: 1024px) {
        // width: 100%;
        width: calc(50% - 40px);

        &:last-child {
            margin-right: auto;
        }
    }
    @media screen and (max-width: 768px) {
        width: 100%;
    }

    div {
        position: relative;
    }

    img {
        width: 100%;
        height: auto;
    }

    a {
        color: black;
        display: flex;
        flex-direction: column;
        height: 100%;

        &:hover {
            text-decoration: none;
            strong {
                color: $color-purple;
            }
        }

        > div {
            padding: 20px;
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            flex-grow: 1;
        }
    }

    span {
        display: block;
        font-size: 12px;
        letter-spacing: 0.25px;
        line-height: 24px;
        text-transform: capitalize;
    }

    strong {
        font-family: $font-headings;
        display: block;
        margin: 0 0 20px;
        font-size: 24px;
        letter-spacing: 0;
        line-height: 32px;
    }

    p {
        margin: 0 0 20px;
        padding: 0;
        font-size: 16px;
        letter-spacing: 0;
        line-height: 24px;
    }

    .meta {
        position: relative;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        font-size: 12px;
        letter-spacing: 0.25px;
        line-height: 24px;
        margin-top: auto;

        &:after {
            position: absolute;
            right: 0;
            bottom: 0;
            width: 15px;
            height: 15px;
            content: "";
            background: url("images/arrow-right-black.svg") no-repeat;
        }

        span {
            position: relative;
            padding: 2px 0 2px 20px;

            em {
                font-style: normal;
            }

            &:after {
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                width: 1px;
                content: "";
                background-color: black;
            }

            &:first-child {
                padding: 2px 20px 2px 0;
            }

            &:last-child {
                &:after {
                    display: none;
                }
            }
        }

        @media screen and (max-width: 1200px) {
            span {
                margin: 0 0 10px !important;
                padding: 0 30px 0 0 !important;
                width: 100%;

                &:after {
                    display: none !important;
                }
            }
        }
    }

    &[data-type="events"],
    &[data-type="voting"] {
        a {
            color: white;

            .meta {
                color: white;
            }

            strong {
                color: white;
            }

            span {
                color: white;
            }

            p {
                color: white;
            }

            > div {
                background-color: transparent !important;
            }

            .meta {
                &:after {
                    background: url("images/arrow-right-white.svg") no-repeat;
                }

                span {
                    &:after {
                        background-color: white;
                    }
                }
            }
        }
    }

    [data-event-date] {
        position: absolute;
        top: 20px;
        left: 20px;
        font-weight: 600;
    }

    &[data-type="events"] {
        strong {
            position: relative;

            &:before {
                position: absolute;
                top: -100px;
                left: 0;
                right: 0;
                height: 25px;
                content: "";
                background: url("images/icon-event.svg") no-repeat 50% 50%;
                background-size: auto 100%;
            }
        }
    }

    &[data-type="voting"] {
        strong {
            position: relative;

            &:before {
                position: absolute;
                top: -100px;
                left: 0;
                right: 0;
                height: 25px;
                content: "";
                background: url("images/icon-voting.svg") no-repeat 50% 50%;
                background-size: auto 100%;
            }
        }
    }

    &[data-type="videos"] {
        iframe {
            height: 230px;
            @media screen and (max-width: 1024px) {
                height: 205px;
                width: 100%;
            }
        }

        .BorlabsCookie {
            padding: 0;
            height: 230px;
            width: 100%;
            @media screen and (max-width: 1024px) {
                height: 205px;
            }

            ._brlbs-content-blocker,
            ._brlbs-embed {
                width: 100%;
                height: 100%;
            }

            ._brlbs-thumbnail {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        > div {
            padding: 20px;

            a {
                strong {
                    position: relative;

                    &:before {
                        position: absolute;
                        top: -100px;
                        left: 0;
                        right: 0;
                        height: 25px;
                        content: "";
                        background: url("images/icon-live.svg") no-repeat 50% 50%;
                        background-size: auto 100%;
                        display: none;
                    }
                }

                p {
                    font-size: 16px;
                    line-height: 24px;

                    &:after {
                        position: absolute;
                        right: 0;
                        bottom: 0;
                        width: 15px;
                        height: 15px;
                        content: "";
                        background: url("images/arrow-right-black.svg") no-repeat;
                    }
                }
            }
        }
    }

    &[data-type="newscategories"] {
        a {
            > div {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                background-color: transparent;
            }
        }

        strong {
            color: white;
        }
    }
}
