.jobs-hero {
  background-image: url(images/bluebyte-jobs-hero-background.jpg);

  .hero-heading {
    margin-bottom: 16px;
  }

  h3 {
    margin-bottom: 64px;
  }
}

.jobs-vacancies {
  &.section-padding-top {
    padding-top: 92px;

    @media screen and (max-width: 768px) {
      padding-top: 28px;
    }
  }

  &.section-padding-bottom {
    padding-bottom: 72px;

    @media screen and (max-width: 768px) {
      padding-bottom: 42px;
    }
  }

  background: $color-light-grey;

  .dropdown-wrapper {
    @media screen and (max-width: 768px) {
      margin-bottom: 16px;
    }
  }

  .mobile-margin-top {
    @media screen and (max-width: 768px) {
      margin-top: 32px;
    }
  }

  .filter-list {
    margin-bottom: 48px;

    @media screen and (max-width: 768px) {
      margin-bottom: 0;

      .order-last {
        margin-top: 32px;
        margin-bottom: 24px;
      }
    }
  }
}

.jobs-shortlist {
  background: $color-white;
  border-radius: 4px;
  //overflow: auto;
  overflow-y: scroll;

  font-size: 12px;
  line-height: 16px;

  .cta {
    font-size: 12px;
    line-height: 16px;
  }

  ul {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  li {
    padding: 16px;

    &:not(:last-child) {
      border-bottom: 1px solid #f0f0f0;
    }

    &.active-job a {
      text-decoration: underline;
    }
  }

  a {
    color: $color-black;
  }
  
  @media screen and (max-width: 767px) {
    max-height: none !important;
  }
}

.jobs-shortlist-add {
  display: flex;
  width: 48px;
  height: 48px;
  align-items: center;
  justify-content: center;

  @media screen and (max-width: 768px) {
    justify-content: flex-end;
  }

  img {
    width: auto;
    height: 16px;
  }
}

.jobs-shortlist-location {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 16px;
}

.jobs-job-details {
  padding: 24px;

  background: $color-white;
  border-radius: 4px;

  ul {
    margin-bottom: 0;
  }

  li {
    color: #545454;

    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
  }
}

.jobs-job-icons {
  a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 48px;
    height: 48px;
  }

  img {
    width: auto;
    height: 16px;
  }
}

.jobs-job-apply-now {
  margin-bottom: 72px;
}

.jobs-job-heading {
  margin-bottom: 32px;
}

.jobs-show-more {
  text-align: center;
}

.jobs-show-more-button.button.dark {
  margin: 48px auto 0;

  span {
    background-image: url(images/arrow-down-black.svg);
  }
}

.jobs-our-studios {
  padding: 104px 0 160px;

  @media screen and (max-width: 768px) {
    padding-top: 48px;
    padding-bottom: 16px;
  }

  h2 {
    margin-bottom: 8px;
  }
}

.jobs-studio-list {
  margin-top: 64px;

  @media screen and (max-width: 768px) {
    margin-top: 32px;
  }

  img {
    width: 100%;
    height: auto;
    margin-bottom: 24px;

    @media screen and (max-width: 768px) {
      margin-bottom: 8px;
    }
  }

  h3 {
    margin-bottom: 8px;
  }

  p {
    margin-bottom: 48px;

    @media screen and (max-width: 768px) {
      margin-bottom: 32px;
    }
  }

  .jobs-studio-cta-container {
    @media screen and (max-width: 768px) {
      margin-bottom: 64px;
      text-align: center;
    }
  }
}

.jobs-benefits {
  h2 {
    margin-bottom: 8px;
  }
}

.jobs-benefit-list-container {
  position: relative;
  width: 100%;
}

.jobs-benefit-list {
  @include make-row;
  margin-top: 64px;
  margin-bottom: 80px;

  @media screen and (max-width: 768px) {
    margin-bottom: 38px;
    white-space: nowrap;
    overflow: auto;
    display: block;
    margin-top: 24px;
  }
}

.jobs-benefit-item {
  @include make-col(2.4);
  padding: 0 15px 15px;

  @media screen and (max-width: 768px) {
    @include make-col(8);
    display: inline-block;
    white-space: normal;
  }

  img {
    width: 100%;
    height: auto;
    margin-bottom: 24px;
  }
}

.jobs-benefit-item-wrapper {
  display: flex;
  flex-direction: column;
}

.jobs-benefits-slider-control {
  position: relative;
  display: none;
  width: 154px;
  height: 2px;
  margin: 0 auto;

  background: #f0f0f0;

  @media screen and (max-width: 768px) {
    display: block;
  }

  &::after {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 50%;
    content: '';

    background: #0092cb;
  }
}
