// Background colors
.bg-plum {
    background-color: $color-plum;
}

.bg-purple {
    background-color: $color-purple;
}

.bg-scroll {
    background-color: $color-scroll;
}

.bg-stone {
    background-color: $color-stone;
}

.bg-blue {
    background-color: $color-blue;
}

// Text colors
.color-plum {
    color: $color-plum;
}

.color-purple {
    color: $color-purple;
}

.color-scroll {
    color: $color-scroll;
}

.color-stone {
    color: $color-stone;
}

.color-blue {
    color: $color-blue;
}

.color-black {
    color: $color-black;
}

// Gradients
.gradient-dark-purple {
    background: linear-gradient(180deg, rgba(25, 1, 12, 0) 0%, #19010C 100%),
    linear-gradient(0deg, rgba(25, 1, 12, 0.5), rgba(25, 1, 12, 0.5));
}
